import { Component, OnInit } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { Usuario } from 'src/app/models/usuario';
import { Router } from '@angular/router';
import { GLOBAL } from 'src/app/services/global';
import * as jsPDF from 'jspdf';
import { Location } from '@angular/common';

@Component({
  selector: 'app-listar-usuario',
  templateUrl: './listar-usuario.component.html',
  styleUrls: ['./listar-usuario.component.css'],
  providers: [UsuarioServiceService]
})
export class ListarUsuarioComponent implements OnInit {
  public identity;
  public usuario: Usuario;
  public usuario_registro: Usuario;
  public token;
  public url: string;
  public titulousuario;
  public titulo;
  public listar: any[] = [];
  public verusuariou: Usuario;
  public bufferCiente: any[] = [];
  constructor(
    private _usuarioServicio: UsuarioServiceService,
    private _router: Router,
    private _location:Location,
  ) {
    this.titulo = " Papeleria San Francisco"
    this.usuario = new Usuario('', '', '', '', '', '', '', '', '', '', '', 'ROL_USER', '', '');
    this.url = GLOBAL.url;
  }




  mensaje() {
    let mensaje;
    if (this.identity.usu_rol == 'ROL_ADMIN') {
      mensaje = 'Gerente'
      //console.log(mensaje);
    }

    if (this.identity.usu_rol == 'ROL_USUARIO') {
      mensaje = 'Empleado'
      //console.log(mensaje);
    }

    return mensaje
  }



  generarPdf() {
    var id = document.getElementById("tabMensaje");

    // var imgData='bjdjt-puacn.webp'
    var pdf = new jsPDF({
      orientacion: '3',
      unit: 'pt',
      format: 'A4',
      posicicion: 3,
    });
    pdf.setFont('times')    //tipo de letra  
    pdf.setFontSize(22);      //tama de letra

    // pdf.addImage(imgData,'JPEG', 15, 40, 18, 16)

    pdf.setTextColor(84, 201, 47)   //color RGB
    pdf.text("Papeleria & Bazar - 'San Francisco' ", 150, 50); //posicion de la letra columna fila

    pdf.setLineWidth(3) //grueso de la linea
    pdf.setDrawColor(251, 152, 4)   // color d ela linea
    pdf.line(80, 67, 525, 67) //  posicion de la linea columna, fila, columna, fila

    // pdf.setTableHeaderRow([1500, 7000]);
    // pdf.table(120, 120, 120, [],120)
    // pdf.table(100, 100,100,[],100)

    pdf.setLineWidth(0.3)
    pdf.setDrawColor(150, 132, 104)
    pdf.setTextColor(7, 8, 6) //color del texto


    // pdf.setTableHeaderRow([10000,5522222])
    pdf.fromHTML(id, 80, 75); //todos los datos de mi tabla  Marca HTML

    pdf.setLineWidth(3) //grueso de la linea
    pdf.setDrawColor(251, 152, 4)   // color d ela linea
    pdf.line(80, 750, 525, 750) //  posicion de la linea columna, fila, columna, fila

    pdf.setFontSize(12);

    pdf.text('Emitido por : ' + this.identity.usu_apellido + ' ' + this.identity.usu_nombre, 80, 775);
        pdf.text('Fecha           : ' + this.today.toLocaleDateString() + "  "+ this.today.getHours()+ ":" + this.today.getMinutes() + ":"+this.today.getSeconds(), 80, 790);

    pdf.save("Lista-Marcas.pdf");
  }
  public today = new Date;

  public countFindeds: number;
  buscasUsuario(termino) {
    termino = termino.toLowerCase().replace(/ /g, '');
    this.listar = [], [];
    this.countFindeds = 0;
    for (const usuario of this.bufferCiente) {
      let nombre = usuario.usu_cedula;

      nombre = nombre + usuario.usu_nombre + usuario.usu_apellido;
      nombre = nombre + usuario.usu_apellido;
      nombre = nombre.toLowerCase().replace(/ /g, '');;
      if (!usuario.usu_cedula) {
        nombre = "0" + nombre;
      }
      if (nombre.indexOf(termino) >= 0) {
        this.countFindeds++;
        this.listar.push(usuario);
      }
    }
  }
  onPrint() {
    window.print();
  }



  ngOnInit() {
    this.identity = this._usuarioServicio.getIdentity();
    this.token = this._usuarioServicio.getToken();
    // console.log(this.identity);
    // console.log(this.token);
    this.getMostrar();
    this.mensaje();
  }

  getMostrar() {
    this._usuarioServicio.getUsuario(this.token).subscribe(

      response => {
        // console.log('la respuesta no hayt')
        if (!response.usuario) {
          //  console.log('error no hay nada')
        }
        else {
          this.listar = response.usuario;
          this.bufferCiente = response.usuario;
          //  console.log(this.listar);
        }
      },
      error => {
        console.log(<any>error);
      }
    )
  }
  regresar() {
    this._location.back();
  }


 
}
