import { Component, OnInit } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { numeroFactura } from 'src/app/models/numero-venta';
import { NumeroVentaService } from 'src/app/services/numero-venta.service';
import { Router } from '@angular/router';
import { Location } from '@angular/common';

@Component({
  selector: 'app-numero-venta',
  templateUrl: './numero-venta.component.html',
  styleUrls: ['./numero-venta.component.css']
})
export class NumeroVentaComponent implements OnInit {
  public identity;
  public token;
  public guardar: any;
  public comprobar: numeroFactura;
  public today = new Date;
  public verNumero: numeroFactura;

  constructor(
    private _usuarioService: UsuarioServiceService,
    private _facturaService: NumeroVentaService,
    private _router: Router,
    private _location:Location,
  ) {
    this.identity = this._usuarioService.getIdentity();
    this.token = this._usuarioService.getToken();
    this.getToday();
    this.initNumero();
    this.comprobarNumero();
    this.ordenarNumero();
    this.rellenar();
  }

  rellenar(){
    this.guardar.num_desde=this.hasta+1;
  }

  regresar(){
    this._router.navigate(['/menu']);
  }

  getToday() {
    this.today.getDate();
  }
  ngOnInit() {
    this.mostrar();
    this.guardar.num_desde=null;
    this.guardar.num_hasta=null;
    this.guardar.num_autorizacion=null;
  
  }
  limpiar(form) {
    this.mensaje = null;
    this.guardar.num_hasta=null;
    this.guardar.num_autorizacion=null;
    this.guardar.num_ambiente=null;
    this.guardar.num_emision=null;
  }
  public com = true;
  public mensaje;
  public exito;
  comprobarNumero() {
     if (this.mostrarnumero && this.mostrarnumero.length >=1) {
       this.mostrarnumero.sort((a,b) => (a.num_desde - b.num_desde));
       this.guardar.num_desde = parseInt(this.mostrarnumero[0].num_hasta + 1);
        //console.log('anteriores');
        } else {
      //  console.log('nuevo');
      this.guardar.num_desde=1;
     }
    if (this.guardar.num_desde > this.guardar.num_hasta) {
      //console.log('numero menor');
      this.com = false;
      this.mensaje = 'El Numero es menor'
    } else {
      this.mensaje = null
      this.com = true;

    }
  }
  public alertRegister;
  initNumero() {
    this.guardar = new numeroFactura('', 0, 0, 0, '', '', 0, '', '', '', '');
  }
  registrar() {
    this.guardar.fk_usuario = this.identity;
    this.guardar.num_fecha = this.today.toString();
    this._facturaService.addNumero(this.token, this.guardar).subscribe(
      response => {
        if (response.numero) {
          this.guardar = response.numero;
          const desde = this.guardar.num_desde;
          const hasta = this.guardar.num_hasta;
          this.alertRegister = 'Se Registro  No.' + desde + ' - ' + hasta + ' Correctamente!..';
          this.initNumero();
          this.mostrar();
          this.guardar.num_desde=null;
          this.guardar.num_hasta=null;
          this.guardar.num_autorizacion=null;
          setTimeout(() => {
            this.alertRegister = null;

          }, 3000);
        }
        this.rellenar();
        this.ordenarNumero();
        
      },
      error => {
        const errorMessage = error as any;
        if (errorMessage != null) {
          const body = JSON.parse(error._body);
          this.alertRegister = body.message;
        }
        setTimeout(() => {
          this.alertRegister = null;
        }, 3000);
      }
    )
  }
  public mostrarnumero: any;

  contador(){
    for (let i = 0; i < this.mostrarnumero.length; i++) {
      const element = this.mostrarnumero[i];
      element.index=i+1;
    }
  }

  mostrar() {
    this._facturaService.getNumero().subscribe(
      response => {
        if (response.numero) {
          this.mostrarnumero = response.numero;
          this.contador();
        }
            
      },
      error => {
        console.log(<any>error);
      }
    )
  }


  // ordenar por fecha los numeros de factura ingresados
  public desde;
  public hasta;
  public otroorden;
  public mas;
  ordenarNumero() {
    this._facturaService.getNumero().subscribe(
      response => {
        response.numero.sort((a, b) => {
          a = new Date(a.num_fecha);
          b = new Date(b.num_fecha);
          return a > b ? -1 : a < b ? 1 : 0;
        })
        this.desde = response.numero[0].num_desde;
        this.hasta = response.numero[0].num_hasta;
        this.otroorden = response.numero[0];
        this.rellenar();
        // console.log('orden desde ', this.desde);
        // console.log('orden hasta ', this.hasta);
        // console.log(this.guardar.num_desde);
        
          
        // ***************pendiente blucle this.autoincrementable**********************
        
      }
      
    )
  }


}
