import { Injectable } from '@angular/core';
//import 'rxjs/add/operator/map';
//import { Observable} from 'rxjs/Observable';
import { GLOBAL } from './global';
import { HttpClientModule, HttpHandler, HttpHeaders, HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import { Response, Http, Headers } from '@angular/http';
import { map } from 'rxjs/operators';
import { Usuario } from '../models/usuario';

@Injectable()
/*@Injectable({
  providedIn: 'root'
})*/

export class UsuarioServiceService {
  public url: string;
  public identity: any;
  public token: string;

  //RUTA DE ACCESO AL BACKEND PARA LOGIN
  constructor(private _http: Http) {
    this.url = GLOBAL.url;
  }

  //********************************INGRESAR EL USUARIO PARA LOGIN******************************************
  //getash devuelve un objeto completo
  //usuario logeado , devuelve el token
  singup(usuario_login, gethash = null) {
    //crear una variable json conviertiendo a un String el objeto recibido
    if (gethash != null) {
      usuario_login.gethash = gethash;
    }
    let json = JSON.stringify(usuario_login);
    let params = json;

    let headers = new Headers({ 'Content-Type': 'application/json' });
    return this._http.post(this.url + 'login', params, { headers: headers }).pipe(map(res => res.json()));
  }
  //*************************IDENTIFICACION DEL USUARIO A TRAVEZ DEL LOGIN ********************************
  //aceder a localstore
  //devolver los datos ya procesados
  getIdentity() {
    //convertir un string que esta guardado en el localstorage a un objeto de java script
    let identity = JSON.parse(localStorage.getItem('identity'));
    if (identity != 'undefined') {
      this.identity = identity;
    }
    else {
      this.identity = null;
    }
    return this.identity;
  }

  //*********************FIN DE IDENTIFICACION DEL USUARIO A TRAVEZ DEL LOGIN ********************************

  //********************************RECIBE EL TOKEN DE AUTENTICACION*****************************
  getToken() {
    //crear variable token para conseguir lo guardado en el localstorage
    let token = localStorage.getItem('token');
    if (token != 'undefined') {
      this.token = token;
    }
    else {
      this.token = null;
    }
    return this.token;
  }
  //****************************FIN DE RECIBE EL TOKEN DE AUTENTICACION*****************************


  //****************************************ACTUALIZAR USUARIO***********************************
  updateUsuario(usuario_actualizar) {
    let json = JSON.stringify(usuario_actualizar);
    let params = json;
    //cabeceras .. tipo de contenido
    //convertir el objeto json en un string
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': this.getToken() });
    return this._http.put(this.url + 'actualizarusuario/' + usuario_actualizar._id, params, { headers: headers }).map(res => res.json());
  }
  //****************************************ACTUALIZAR USUARIO NUEVO***********************************
  editarUsuario(id: string, usuario: Usuario) {
    let json = JSON.stringify(usuario);
    let params = json;
    //cabeceras .. tipo de contenido
    //convertir el objeto json en un string
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': this.getToken() });
    return this._http.put(this.url + 'actualizarusuario/' + id, params, { headers: headers }).map(res => res.json());
  }
  //************************************FIN DE ACTUALIZAR USUARIO***********************************
  getUsuario(token) {
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'mostrar-usuario', { headers: headers }).map(res => res.json());
  }

  mostrarUnUsuario(_id: string) {
    let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'get-usuario/' + _id, { headers: headers }).map(res => res.json());


  }
  editarUnUsuario(_id: string, usuario: Usuario) {
    let token = localStorage.getItem('token');
    let params = JSON.stringify(usuario);
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.put(this.url + 'update-usuario/' + _id, params, { headers: headers }).map(res => res.json());
  }

  comprobarContrase(contrase) {
    this.getIdentity();
    let params = JSON.stringify({contrase: contrase});
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': this.getToken()  });
    return this._http.post(this.url + 'comprobar-contrase/'+this.identity._id, params, { headers: headers }).pipe(map(res => res.json()));
  }

  cambiarContrase(id: string, usuario) {
    let token = localStorage.getItem('token');
    let params = JSON.stringify(usuario);
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.put(this.url + 'cambiar-contrase/' + id, params, { headers: headers }).map(res => res.json());
  }

}
