import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { GLOBAL } from './global';
import { Proveedor } from '../models/proveedor';
import { Cliente } from '../models/cliente';

@Injectable({
  providedIn: 'root'
})
export class ClienteService {
  public  url:string;

  constructor(
    private _http:Http
  ) { 
    this.url=GLOBAL.url;
  }
  
  addCliente(cliente: Cliente) {
    let token = (localStorage.getItem('token'));
    let params = JSON.stringify(cliente)
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.post(this.url + 'guardarcliente', params, { headers: headers }).map(res => res.json());
  }
  //mostrar todos los datos
  getCliente(token) {
    //let token=localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'mostrarcliente', { headers: headers }).map(res => res.json());
  }
  //eliminar marca
  deleteCliente(id: string) {
    let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.delete(this.url + 'deletecliente/' + id, { headers: headers }).map(res => res.json());;
  }
  //mostrar un producto

  mostrarUnCliente(id: string) {
    let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'mostraruncliente/' + id, { headers: headers }).map(res => res.json());
  }

  editarCliente(id: string, cliente: Cliente) {
    let token = localStorage.getItem('token');
    let params = JSON.stringify(cliente);
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.put(this.url + 'updatecliente/' + id, params, { headers: headers }).map(res => res.json());
  }

}

