  public nombre;
  public cadenas;
    codigoAletorio(){
      let chars='0123456789';
      let size='5';
      let cadena='';
      for( let i=0;i<5;i++){
        cadena+=chars[Math.floor(Math.random()*(chars.length))];
      }
      this.nombre="PRO"
      this.cadenas=cadena;
      this.producto_registro.prod_codigo=this.nombre+this.cadenas;
      // console.log(this.producto_registro.prod_codigo);
    }

