  findMarca(termino) {
    termino = termino.toLowerCase().replace(/ /gi, '');
    // termino = termino.replace(/ /gi, '');
    this.mostrar_marca = [];
    for (const marca of this.bufferMarca) {
      let busqueda = marca.marca_codigo;
      busqueda = busqueda + marca.marca_descripcion;
      busqueda = busqueda.toLowerCase();
      busqueda = busqueda.replace(/ /gi, '');
      if (busqueda.indexOf(termino) >= 0) {
        this.mostrar_marca.push(marca);
      }
    }
    if (termino == '') {
      // console.log('rellenando marcas: ',this.bufferMarca);
      this.mostrar_marca = this.bufferMarca;
    }
  }
  findCategoria(termino) {
    termino = termino.toLowerCase();
    termino = termino.replace(/ /gi, '');
    this.marcaCategoria=[];
    for (const categoria of this.bufferCategoria) {
      let busqueda = categoria.cat_codigo;
      busqueda = busqueda + categoria.cat_descripcion;
      busqueda = busqueda.toLowerCase();
      busqueda = busqueda.replace(/ /gi, '');
      if (busqueda.indexOf(termino) >= 0) {
        this.marcaCategoria.push(categoria);
      }

    }
    if (termino == '') {
      this.marcaCategoria = this.bufferCategoria;
    }
  }
