import { Component } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import * as moment from 'moment';
import { KardexService } from 'src/app/services/kardex.service';
import { ProductoService } from 'src/app/services/producto.service';

@Component({
  selector: 'app-saldos',
  templateUrl: './saldos.component.html',
  styleUrls: ['./saldos.component.css'],
  providers: [UsuarioServiceService, KardexService, ProductoService]
})
export class SaldosComponent {
  public identity;
  public token;
  public today: Date;
  public consolidado;
  public bufferConsolidado;
  public productos;
  public countFindeds;
  constructor(
    private _usuarioService: UsuarioServiceService,
    private _kardexService: KardexService,
    private _productServce: ProductoService
  ) {
    this.identity = this._usuarioService.getIdentity();
    this.token = this._usuarioService.getToken();
    this.today = new Date();
    this.countFindeds = 0;
    this.initProductos();
  }

  initProductos() {
    this._productServce.getProducto(this.token).subscribe(
      response => {
        this.productos = response.producto;
          this.bufferProducto = response.producto;
        //console.log('producto', this.productos,'buffer',this.bufferProducto);
        
        this.consolidado = [];
        for (const producto of this.productos) {
          this.initConsolidado(producto._id);
        }
        //console.log(this.consolidado);
        this.bufferConsolidado = this.consolidado;
        console.log(this.bufferConsolidado);
        
      }
    )
  }

  initConsolidado(id) {
    this._kardexService.getKardexByProduct(this.token, id).subscribe(
      response => {
        if (response.kardex) {
          console.log(response);
          
          const kardex = response.kardex.sort((a,b) => {
              const fecha1 = moment(a.kardex_fecha);
              const fecha2 = moment(b.kardex_fecha);
              if (fecha1 > fecha2) return -1;
              if (fecha1 < fecha2) return 1;
              if (fecha1 == fecha2) return 0;
            });
          this.consolidado.push(kardex[0]);
        }
      }
    )
  }

  public bufferProducto: any[] = [];

  buscarProducto(termino) {
    termino = termino.toLowerCase().replace(/ /g, '');
    // this.productos = [], [];
    this.consolidado = [];
    this.countFindeds = 0;
    for (const consolidado of this.bufferConsolidado) {
      let nombre = consolidado.kardex_fk_producto.prod_descripcion;
      nombre = nombre + consolidado.kardex_fk_producto.prod_codigo;
      nombre = nombre.toLowerCase().replace(/ /g, '');
      if (nombre.indexOf(termino, 0) >= 0) {
        this.countFindeds++;
        this.consolidado.push(consolidado);
      }
    }
  }

}





