import { Component, OnInit, DoCheck } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { Compra } from 'src/app/models/compra';
import { CompraService } from 'src/app/services/compra.service';
import { ProductoService } from 'src/app/services/producto.service';
import { ProveedorService } from 'src/app/services/proveedor.service';
import { Proveedor } from 'src/app/models/proveedor';
import { IvaService } from 'src/app/services/iva.service';
import { detalleCompra } from 'src/app/models/detalleCompra';
import { DetalleCompraService } from 'src/app/services/detalle-compra.service';
import { StatisticsService } from 'src/app/services/statistics.service';
import { KardexService } from 'src/app/services/kardex.service';
import { Kardex } from 'src/app/models/kardex';
import Swal from 'sweetalert2'


@Component({
  selector: 'app-compra',
  templateUrl: './compra.component.html',
  styleUrls: ['./compra.component.css'],
  providers: [KardexService, UsuarioServiceService, CompraService, ProductoService, DetalleCompraService, StatisticsService],
})
export class CompraComponent implements DoCheck {
  public token;
  public identity;
  public compra: Compra;
  public productos: any[] = [];
  public bufferProducto: any[] = [];
  public ivaActual: number;
  public listarProveedor: Proveedor;
  public sinProducto;
  public iva: any;
  public sinDatos: boolean;
  public numeroCorrecto: boolean;
  public ultimaFactura: string;
  public getToday = new Date()
  public today: Date;
  public tempFecha: string;
  // '2019-12-05';
  constructor(
    private _proveedorService: ProveedorService,
    private _productoService: ProductoService,
    private _usuarioService: UsuarioServiceService,
    private _ivaService: IvaService,
    private _compraService: CompraService,
    private _detalleCompraService: DetalleCompraService,
    private _statisticsService: StatisticsService,
    private _kardexService: KardexService

  ) {
    this.today = new Date(this.getToday);
    this.token = this._usuarioService.getToken();
    this.identity = this._usuarioService.getIdentity();
    this.initIvaActual();
    this.listarProducto();
    this.initProductoIngresar();
    this.mostrarProveedor();
    this.initCompra();
    this.sinProducto = false;
    this.sinDatos = true;
    this.iva = 'sin iva....';
    this.ultimaFactura = 'Seleccionar un proveedor';
    this.tempFecha = this.today.toString();
  }

  public cantCorrecta;
  negativo() {
    this.cantCorrecta = false;
    if (this.productoIngresar != null && this.productoIngresar.cajas < 1) {
      Swal.fire({
        icon: 'error',
        title: 'Oops...',
        text: 'No se Admiten nmeros negativo o Cero!',
        showConfirmButton: false,
        timer: 1500,
        position: 'top',
        grow: 'row',
        backdrop: false,
        toast: true
      })
      this.productoIngresar.cajas = 1;
      this.cantCorrecta = true;
    } else {
      this.cantCorrecta = true;
    }
  }




  ngDoCheck() {
    this.today = new Date();
  }
  validarIngreso() {
    if (
      (this.productoIngresar.unidad_caja != null && this.productoIngresar.unidad_caja != '') &&
      (this.productoIngresar.cajas != null && this.productoIngresar.cajas != '') &&
      (this.productoIngresar.precio_caja != null && this.productoIngresar.precio_caja != '')
    ) {
      this.sinDatos = false;
    } else {
      this.sinDatos = true;
    }
  }
  initIvaActual() {
    this._ivaService.getIva(this.token).subscribe(
      response => {
        response.iva.sort((a, b) => {
          a = new Date(a.iva_fecha);
          b = new Date(b.iva_fecha);
          return a > b ? -1 : a < b ? 1 : 0;
        })
        this.ivaActual = response.iva[0].iva_valor;
        this.iva = response.iva[0];
        // console.log("este es el iva"+this.ivaActual);
      }
    )
  }
  initCompra() {
    this.compra = {
      _id: '',
      com_fechaEmitida: this.today.toString(),
      com_fechaIngreso: this.today.toString(),
      com_codigo: 0,
      com_subtotal: 0,
      com_total: 0,
      com_iva: 0,
      fk_proveedor: '',
      fk_usuario: this.identity._id,
      fk_ivas: ''
    }
  }
  mostrarProveedor() {
    this._proveedorService.getProveedor(this.token).subscribe(
      response => {
        this.listarProveedor = response.proveedor;
      },
      error => {
        console.log(error as any);
      }
    )
  }
  initProductoIngresar() {
    this.productoIngresar = {
      _id: '',
      prod_codigo: '',
      prod_descripcion: '',
      prod_precioCompra: '',
      prod_precioVenta: '',
      prod_fecha: '',
      prod_fechaActualizacion: '',
      fk_marca: '',
      fk_categoria: '',
      prod_stock: '',
      iva: '',
      unidad_caja: '',
      cajas: '',
      stock_sig: '',
      stock_ing: '',
      precio_caja: '',
      total: '',
      subtotal: '',
      valorIva: ''
    }
  }
  listarProducto() {
    this.sinProducto = false;
    this._productoService.getProducto(this.token).subscribe(
      response => {
        this.productos = response.producto;
        this.bufferProducto = response.producto;
        if (response.status == 404 || !response.producto) {
          this.sinProducto = true;
          // console.log('sin producto' + response.status);
        } else {
          this.sinProducto = false;
        }
      },
      error => {
        console.log(error.status);
        this.sinProducto = true;
        console.log(error as any);
      }
    )
  }
  buscarProducto(termino) {
    termino = termino.toLowerCase().replace(/ /g, '');
    this.productos = [], [];
    for (const prod of this.bufferProducto) {
      let nombre = prod.prod_codigo;
      nombre = nombre + prod.prod_descripcion;
      nombre = nombre.toLowerCase().replace(/ /g, '');;
      if (!prod.prod_codigo) {
        nombre = "0" + nombre;
      }
      if (nombre.indexOf(termino) >= 0) {
        this.productos.push(prod);
      }
    }
  }
  public productoIngresar;
  agregarProducto(producto) {
    this.initProductoIngresar();
    this.productoIngresar = producto;
    this.productoIngresar.cajas = '';//unidades
    this.productoIngresar.stock_sig = '';
    this.productoIngresar.precio_caja = '';//precio de venta
    this.productoIngresar.total = '';
    this.productoIngresar.subtotal = 0;
    this.productoIngresar.ingUnidades = true;
    this.productoIngresar.unidad_caja = 1;
  }
  calculokardex() {

  }
  calcularStock() {
    const stock_diff = parseInt(this.productoIngresar.cajas) * parseInt(this.productoIngresar.unidad_caja);
    this.productoIngresar.stock_ing = stock_diff;
    this.productoIngresar.stock_sig = parseInt(this.productoIngresar.prod_stock) + stock_diff;
  }
  calcularTotalCaja() {
    if (this.productoIngresar.iva && this.productoIngresar.iva === true) {
      let total = parseFloat(this.productoIngresar.cajas) * parseFloat(this.productoIngresar.precio_caja);
      let iva = this.ivaActual / 100;
      this.productoIngresar.valorIva = total * iva;

      // this.soloiva = this.productoIngresar.valorIva;
      this.productoIngresar.subtotal = total - this.productoIngresar.valorIva;
      this.productoIngresar.total = total;
    } else if (!this.productoIngresar.iva || this.productoIngresar.iva === false) {
      this.productoIngresar.valorIva = 0;
      this.productoIngresar.total = parseFloat(this.productoIngresar.cajas) * parseFloat(this.productoIngresar.precio_caja);
      this.productoIngresar.subtotal = this.productoIngresar.total;
    }
  }
  cacularTotales() {
    // this.productoIngresar.cajas = parseInt((this.productoIngresar.cajas.toFixed(0)));
    // alert(this.productoIngresar.cajas)
    this.calcularTotalCaja();
    this.calcularStock();
    this.validarIngreso();
  }
  calcularFactura() {
    this.compra.com_total = 0;
    this.compra.com_iva = 0;
    this.compra.com_subtotal = 0;
    for (const producto of this.productoCompra) {
      // this.compra.com_iva = this.compra.com_iva + producto.valorIva;
      this.compra.com_iva = this.compra.com_iva + producto.valorIva;
      this.compra.com_subtotal = this.compra.com_subtotal + producto.subtotal;
      this.compra.com_total = this.compra.com_total + producto.total;
    }
  }
  public kardex: Kardex;
  initKardex() {
    this.kardex = {
      _id: null,
      kardex_fecha:null,
      kardex_detalle:null,
      kardex_fk_producto      : null,
      kardex_valor_unitario:null,
      kardex_ingreso: {
      ingreso_cantidad: null,
      ingreso_valor: null
      },
      kardex_salida: {
      salida_cantidad: null,
      salida_valor: null
      },
      kardex_saldos: {
      saldo_cantidad: null,
      saldo_valor: null
      },
      kardex_promedio:null
    }
  }
  onSubmit() {
    this.today = new Date();
    this.tempFecha = this.compra.com_fechaEmitida;
    
    // if (confirm("Desea Ingresar")) {
    // console.log(this.compra);
    this.compra.com_fechaIngreso = this.today.toString();
    if(!this.compra.com_fechaEmitida){
      this.compra.com_fechaEmitida=this.today.toString();
    }
    this.compra.fk_ivas = this.iva._id;
    this._compraService.addCompra(this.compra).subscribe(
      response => {
        this.tempFecha = response.compra.com_fechaEmitida;
        // alert(response.message);
        if (this.productoCompra.length >= 1) {
          for (const compra of this.productoCompra) {
            this._kardexService.getKardexByProduct(this.token, compra._id).subscribe(
              response => {
                this.initKardex();
                let kardexes = response.kardex;
                if (response.kardex && response.kardex.length >= 1) {
                    kardexes = kardexes.sort((menor, mayor) => {
                      const a = new Date(menor.kardex_fecha);
                      const b = new Date(mayor.kardex_fecha);
                      if (a == b) return 0
                      if (a > b) return -1
                      if (a < b) return 1
                    })
                }
                
                const valorIngreso                = compra.precio_caja * compra.stock_ing;
                this.kardex.kardex_fecha          = this.compra.com_fechaIngreso;
                this.kardex.kardex_detalle        = this.compra.com_codigo.toString();
                this.kardex.kardex_fk_producto    = compra._id,
                this.kardex.kardex_valor_unitario = compra.precio_caja,
                this.kardex.kardex_ingreso        = {
                      ingreso_cantidad              : compra.stock_ing,
                      ingreso_valor                 : valorIngreso
                };
                let cantidad: number;
                let valorSaldo: number;
                let promedio: number;
                if (kardexes && kardexes.length >= 1) {
                  let kardexAnterior  = kardexes[0];
                  cantidad            = kardexAnterior.kardex_saldos.saldo_cantidad + compra.stock_ing;
                  valorSaldo          = kardexAnterior.kardex_saldos.saldo_valor + valorIngreso;
                }
                else if (!kardexes || kardexes.length <= 0) {
                  cantidad            = compra.stock_ing;
                  valorSaldo          = valorIngreso;
                }
                promedio              = valorSaldo / cantidad;
                this.kardex.kardex_saldos       = {
                  saldo_cantidad                  : cantidad,
                  saldo_valor                     : valorSaldo
                };
                this.kardex.kardex_promedio     = promedio;
                this._kardexService.addKardex(this.token, this.kardex).subscribe(
                  response => {
                    //console.log(response);
                    const productoEditar = {
                      id: compra._id,
                      prod_precioVenta: promedio
                    };
                    
                    this._productoService.actualizarPromedio(productoEditar).subscribe(
                      response => {
                        //console.log(response);
                      }
                    );
                  }, error => {
                    console.log(error as any);
                  }
                )
              }
            );
            this.initDetalleCompra(compra, response);
            this.limpiar();
          }
        }
      }, error => {
        console.error(error as any);

      }
    )
    // }

  }
  verFecha(fecha) {
    //console.log(fecha)
  }
  initDetalleCompra(producto, response) {
    let precioCompra: number;
    precioCompra = producto.total / producto.stock_ing;
    const detalleCompra: detalleCompra = {
      _id: '',
      det_detalle: 'Detalle de la factura: ' + response.compra.com_codigo,
      det_fecha: this.tempFecha,
      det_stockIng: producto.stock_ing,
      det_cajas: producto.cajas,
      det_unidadesCaja: producto.unidad_caja,
      det_precioCaja: producto.precio_caja,
      det_subtotal: producto.subtotal,
      det_total: producto.total,
      det_iva: producto.valorIva,
      det_precioCompraActual: producto.prod_precioCompra,
      det_precioVentaActual: producto.prod_precioVenta,
      fk_producto: producto._id,
      fk_compra: response.compra._id,
    }
    this.ingresarDetalleCompra(detalleCompra);
  }

  ingresarDetalleCompra(detalleCompra) {
    this._detalleCompraService.guardarDetalleCompra(detalleCompra).subscribe(
      response => {
        // console.log(response);
      }
    )
  }

  cambioIngreso() {
    if (this.productoIngresar.ingUnidades === true) {
      this.productoIngresar.unidad_caja = 1;
    } else if (this.productoIngresar.ingUnidades === false) {
      this.productoIngresar.unidad_caja = null;
    }
  }
  public productoCompra: any[] = [];
  ingresarProducto() {
    this.productoCompra.push(JSON.parse(JSON.stringify(this.productoIngresar)));
    for (let i = 0; i < this.bufferProducto.length; i++) {
      const producto = this.bufferProducto[i];
      if (producto._id === this.productoIngresar._id) {
        this.bufferProducto.splice(i, 1);
        this.productos = this.bufferProducto;
        this.initProductoIngresar();
        this.calcularFactura();
        this.sinDatos = true;
      }
    }
  }

  borrarProducto(producto, index) {
    const tempProducto = {
      fk_categoria: producto.fk_categoria,
      fk_marca: producto.fk_marca,
      prod_codigo: producto.prod_codigo,
      iva: producto.iva,
      prod_descripcion: producto.prod_descripcion,
      prod_fecha: producto.prod_fecha,
      prod_fechaActualizacion: producto.prod_fechaActualizacion,
      prod_precioCompra: producto.prod_precioCompra,
      prod_precioVenta: producto.prod_precioVenta,
      prod_stock: producto.prod_stock,
      __v: producto.__v,
      _id: producto._id
    }
    this.bufferProducto.push(tempProducto);
    this.productoCompra.splice(index, 1);
  }
  limpiar() {
    this.compra.fk_proveedor = "";
    this.compra.com_codigo = 0;
    this.ultimaFactura = "Seleccionar un proveedor";
    this.compra.com_fechaEmitida = "";
    for (let i = 0; i < this.productoCompra.length; i++) {
      const producto = this.productoCompra[i];
      const tempProducto = {
        fk_categoria: producto.fk_categoria,
        fk_marca: producto.fk_marca,
        prod_codigo: producto.prod_codigo,
        iva: producto.iva,
        prod_descripcion: producto.prod_descripcion,
        prod_fecha: producto.prod_fecha,
        prod_fechaActualizacion: producto.prod_fechaActualizacion,
        prod_precioCompra: producto.prod_precioCompra,
        prod_precioVenta: producto.prod_precioVenta,
        prod_stock: producto.prod_stock,
        __v: producto.__v,
        _id: producto._id
      }
      this.bufferProducto.push(tempProducto);
    }
    this.productoCompra = [];
  }
  /**
   * Comprobador del numero de factura, ingresa el numero de factura y se envia al servicio el id del proveedor
   * @param numeroFactura
   */
  comprobarNumero(numero) {
    this.numeroCorrecto = false;
    if (this.compra.fk_proveedor) {
      this._statisticsService.getNumerosCompras(this.compra.fk_proveedor).subscribe(
        response => {
          if (!response.numeros || response.numeros.length <= 0) {
            response.numeros = [{
              com_codigo: 0
            }];
          }
          // console.log(response.Message);
          response.numeros.sort((a, b) => (b.com_codigo - a.com_codigo));
          this.ultimaFactura = response.numeros[0].com_codigo;
          let conteo = 0;
          // console.log(response.numeros);

          if (numero != '') {
            for (const facturas of response.numeros) {
              const numeros = facturas.com_codigo;
              if (numero == numeros) {
                conteo++;
              }
            }

            if (conteo == 0) {
              this.numeroCorrecto = true;
            } else if (conteo >= 1) {
              this.numeroCorrecto = false;
            }
          }
        }
      )
    }
  }
}
