import { Component, OnInit } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { VentaService } from 'src/app/services/venta.service';
import { DetalleVentaService } from 'src/app/services/detalle-venta.service';
import { detalleVenta } from 'src/app/models/detalleVenta';
import { Venta } from 'src/app/models/venta';
import { StatisticsService } from 'src/app/services/statistics.service';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import * as moment from 'moment';


@Component({
  selector: 'app-factura-venta',
  templateUrl: './factura-venta.component.html',
  styleUrls: ['./factura-venta.component.css'],
  providers: [UsuarioServiceService, DetalleVentaService, StatisticsService]
})
export class FacturaVentaComponent implements OnInit {
  public identity;
  public token;
  public today = new Date;
  public asc: boolean;
  constructor(
    private _usuarioService: UsuarioServiceService,
    private _ventadetalleService: DetalleVentaService,
    private _ventaService: VentaService,
    private _statisticsService: StatisticsService,
    private _location: Location,
    private _router: Router,
  ) {
    this.asc = true;
    this.token = this._usuarioService.getToken();
    this.identity = this._usuarioService.getIdentity();
    this.estadisticas();
  }


  
  regresar() {
    this._router.navigate(['/reporte']);
  }
  public ventas: Venta;
  public totalsuma: number;
  public statistics;

  estadisticas() {
    this._statisticsService.getUserStatistics().subscribe(
      response => {
        this.statistics = response.conteo;
      }
    );
  }
  ventaTotal() {
    this._statisticsService.getTotalVenta().subscribe(
      response => {
        this.ventas = response.venta;
        let suma = 0;
        for (const total of response.numeros) {
          const numeros = total.ven_total;
          suma = suma + numeros;
        }
        this.totalsuma = suma;
      },
      error => {
      }
    )
  }
  ngOnInit() {
    // this.getdetalleventa();
    this.mostrarVenta();
    this.ventaTotal();
  }

  onPrint() {
    window.print();
  }

  public venta: any[];
  mostrarVenta() {
    this._ventaService.getVenta(this.token).subscribe(
      response => {
        if (response.venta) {
          this.venta = response.venta;
          this.buffer=response.venta;
          this.reordenarArray('codigo');
          this.buscarFecha();
        }
      },
      error => {
      }
    )
  }
  cambiarOrden(celda) {
    if (this.asc == true) this.asc = false; else
      if (this.asc == false) this.asc = true;
    this.reordenarArray(celda);
  }
  reordenarArray(celda) {
    if (this.asc == true) {
      if (celda == 'codigo') {
        this.venta.sort((menor, mayor) => (parseInt(mayor.ven_codigo) - parseInt(menor.ven_codigo)));
      } else if (celda == 'total') {
        this.venta.sort((menor, mayor) => ((mayor.ven_total) - (menor.ven_total)));
      } 
      // else if (celda == 'nombre') {
      //   console.log('ordenando por nombre');
      //   this.venta.sort((menor, mayor) => (mayor.fk_cliente.cli_nombre - (menor.fk_cliente.cli_nombre)));
      // }
    } else if (this.asc == false) {
      if (celda == 'codigo') {
        this.venta.sort((menor, mayor) => (parseInt(menor.ven_codigo) - parseInt(mayor.ven_codigo)));
      } else if (celda == 'total') {
        this.venta.sort((menor, mayor) => (menor.ven_total - mayor.ven_total));
      } 
      // else if (celda == 'nombre') {
      //   console.log('ordenando por nombre');
      //   this.venta.sort((menor, mayor) => (menor.fk_cliente.cli_nombre - mayor.fk_cliente.cli_nombre));
      // }
    }
    for (let i = 0; i < this.venta.length; i++) {
      const element = this.venta[i];
      element.index = i + 1;
    }
  }
  
  public detallev: any;
  public buffer: any[] = [];

  // getdetalleventa() {
  //   this._ventadetalleService.getdetalleventa().subscribe(
  //     response => {
  //       if (response.detalleventa) {
  //         this.detallev = response.detalleventa;
  //         this.buscarFecha();
  //       }
  //     },
  //     error => {
  //     }
  //   )
  // }

  limpiar(){
    this.fechaInicio=null;
    this.fechaFin=null;
    this.fechaInicio='';
    this.fechaFin='';
    //console.log('limpiar cargado');
   this.venta = this.buffer;
   this.sumatotal= '';
   this.numero='';
   this.alertar = '';
    
  }

  public fechaInicio;
  public fechaFin;
  public asd
  public variabletemporal=[];
  public sumatotal;
  public numero;
  public alertar;
  buscarFecha() {
    //console.log('inicio', this.fechaInicio, 'fin', this.fechaFin);

    if (this.fechaFin && this.fechaInicio) {
      let inicio = moment(this.fechaInicio);
      let fin = moment(this.fechaFin);

      if (inicio > fin) {
        alert('La fecha de inicio no puede ser anterior a la de fin');
      } else {
        this.asd = [];
        for (let i = 0; i < this.buffer.length; i++) {
          const element = this.buffer[i];
          const fecha = moment(element.ven_fecha_venta);
          const defffechaInicio = fecha.diff(inicio, 'days');
          const defffechafin = fin.diff(fecha, 'days');
         // console.log('Dif In', defffechaInicio, 'Df Fin' ,defffechafin, 'Fecha: ', element.ven_fecha_venta);
          
          if (defffechaInicio >= 0 && defffechafin >= 0) {
            // console.log('fecha', element.det_fecha, '.inicio: ', defffechaInicio, '. fin: ', defffechafin);
            this.asd.push(element);
            // suma=suma+this.asd.
            // console.log(this.asd);
          }
          // else{
          //  this.alertar='No hay Ventas en esa Fecha';
          //  setTimeout(() => {
          //   this.alertar = null;
          // }, 2500);
          // }
        }
          this.venta=this.asd;
          // console.log('valor temporal', this.venta);
          
          let suma=0
          let num=0;
          for (let i = 0; i < this.venta.length; i++) {
            const element = this.venta[i];
             suma=suma+element.ven_total;
             num=num+1;
          }
          // console.log(' la suma ess',suma);
          
          // comprobacion en caso de que exista algo en el array la alerta se borra
          if (this.venta && this.venta.length >= 1 ) {
            this.alertar = null;
          // si no hay nada en el array se muestra la alerta
          } else if (!this.venta || this.venta.length <= 0) {
            this.alertar = 'No se Encuentran Coincidencias';
          }

          this.sumatotal= suma;
          this.numero=num
         // console.log(this.numero);
          //console.log('al fin',this.venta);
          for (let i = 0; i < this.venta.length; i++) {
            const element = this.venta[i];
            element.index=i+1;
            
          }
      }
    }
  }


}
