import { Component, OnInit } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Marca } from 'src/app/models/marca';
import { MarcaService } from 'src/app/services/marca.service';
import { GLOBAL } from 'src/app/services/global';
import { Location } from '@angular/common';

@Component({
  selector: 'app-editar-marca',
  templateUrl: './editar-marca.component.html',
  styleUrls: ['./editar-marca.component.css']
})
export class EditarMarcaComponent implements OnInit {
  public token;
  public identity;
  public editar_marca: Marca;
  public url: string;
  public alertRegister;
  public today = new Date;

  constructor(
    private _usuarioServicio: UsuarioServiceService,
    private _router: Router,
    private _marcaServicio: MarcaService,
    private _route: ActivatedRoute,
    private _location:Location

  ) {
    this.token = this._usuarioServicio.getToken();
    this.identity = this._usuarioServicio.getIdentity();
    this.url = GLOBAL.url;
    this.getToday();

  }

  getToday() {
    this.today.getDate();
  }

  ngOnInit() {
    this.getMostrarMarca();
  }

  regresar() {
    // this._router.navigate(['marca'])
    this._location.back();
  }

  getMostrarMarca() {
    this._route.params.forEach((params: Params) => {
      let id = params['id'];
      this._marcaServicio.mostrarUnaMarca(id).subscribe(
        response => {
          if (!response.editar_marca) {

            this.editar_marca = response.marca;
          }
        },
        error => {
          var errorMessage = <any>error;
          if (errorMessage != null) {
            var body = JSON.parse(error._body)
            this.alertRegister = body.message;
            console.log(error);
          }
        }
      )
    })
  }
  actualizar() {
    // if (confirm('Desea Guardar')) {
      this.editar_marca.marca_modificado=this.identity;
      this.today = new Date;

      this.editar_marca.marca_fechaActualizacion = this.today.toString();
      this._route.params.forEach((params: Params) => {
        let id = params['id'];
        this._marcaServicio.editMarca(id, this.editar_marca).subscribe(
          response => {
            if (response.marca) {
              this.alertRegister = "Actualizado Correctamente"
              // alert('La Marca se ha actualizado correctamente');

            }
            else {
              console.log('error');
              
            }
          }
        )
      })
    // }
  }
  
}
