import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
// import { Body } from '@angular/http/src/body';
import { GLOBAL } from 'src/app/services/global';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { Usuario } from 'src/app/models/usuario';
import Swal from 'sweetalert2'


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers: [
    UsuarioServiceService
  ]
})
export class LoginComponent implements OnInit {

  public identity;
  public usuario: Usuario;
  public usuario_registro: Usuario;
  public token;
  public url: string;
  public titulousuario;
  public titulo;
  public status;
  errorMessage;
  exito;

  // asignar un valor a una propiedad de una clase
  // darle un valor al objeto usuario
  constructor(
              private _usuarioServicio: UsuarioServiceService,
              private _route: ActivatedRoute,
              private _router: Router,
              ) {
               this.titulo = ' Papeleria San Francisco';
                this.usuario = new Usuario('', '', '', '', '', '', '','', '', '', '', 'ROL_USER', '', '');
              //  this.usuario_registro = new Usuario('','','','','','','','ROLE_USER','');
                this.url = GLOBAL.url;
               this.titulousuario = this.usuario.usu_usuario;
              
              }

  // public usuario = Usuario; //nuevo objeto
  // todo los objetos del usuario que este identificado dentro del login
  // darle el valor del objeto, si esta relleno va a estar logueado


  ngOnInit() {
      // asignar un valor
      this.identity = this._usuarioServicio.getIdentity();
      if (this.identity !== '' && this.identity !== null && this.identity !== undefined ) {
        this._router.navigate(['/inicio']);
      }
      this.token = this._usuarioServicio.getToken();
      // console.log(this.identity);
      // console.log(this.token);0
  }

  // logeuear al usuario
  public onSubmit() {
    // console.log(this.usuario);

    // consigue los datos del usuario identificado
    this._usuarioServicio.singup(this.usuario).subscribe(
      response => {
        // console.log(response);
        const identity = response.usuario;
        this.identity = identity;
        if (!this.identity._id) {
          alert('El usuario no esta correctamente Identificado');
        } else {
          // crear elemento  en el localstorage para tener al usuario en sesion
          // definir un elemento en el localstorage

            localStorage.setItem('identity', JSON.stringify(identity)); // objeto del usuario identificado
          // conseguir el token para enviarselo a cada peticion http
            this._usuarioServicio.singup(this.usuario, 'true').subscribe(
                  responseUsuario => {

                    // console.log(responseUsuario);
                    const token = responseUsuario.token;
                    this.token = token;

                    if (this.token.lenght <= 0 ) {
                      alert('El token no se ha generado');

                    } else {
                        // crear un localstoraged para tener el token disponible
                        Swal.fire({
                          title: 'Bienvenido!',
                          text: this.identity.usu_nombre.toUpperCase() +' '+this.identity.usu_apellido.toUpperCase(),
                          //imageUrl: '{{identity.usu_imagen}}',
                          imageWidth: 400,
                          imageHeight: 200,
                          //imageAlt: 'Custom image',
                          showConfirmButton:false,
                          timer : 1500,
                        })

                        //alert('Bienvenido' + ' '+this.identity.usu_nombre.toUpperCase() +' '+this.identity.usu_apellido.toUpperCase() );
                        localStorage.setItem('token', token);
                        this.usuario = new Usuario('', '', '','', '', '', '', '', '', '', '', 'ROL_USER', '', '');

                        this._router.navigate(['/inicio']);
                    }
                  },
                  error => {
                    const errorMessage =  error as any;
                    if (errorMessage != null) {
                      const body = JSON.parse(error._body);
                      this.errorMessage = body.message;
                      console.log(error);
                    }
                  }
                );

        }

      },
      error => {
        const errorMessage = error as any;
        if (errorMessage != null) {
          const body = JSON.parse(error._body);
          this.alertRegister = body.message;
          console.log(error);
        }
        alert(this.alertRegister);
        this.usuario_registro = new Usuario('', '','', '', '', '', '', '', '', '', '', 'ROL_USER', '', '');
      }

    );
  }
  public alertRegister;
  registrarse() {
    this.identity.navigate(['/registrarse']);
  }
  lougout() {
    localStorage.removeItem('identity');
    localStorage.removeItem('token');
    localStorage.clear();
    this.identity = null;
    this.token = null;
    this._router.navigate(['/']);
  }

}
