import { Injectable } from '@angular/core';
import { Http,Headers } from '@angular/http';
import { GLOBAL } from './global';
import 'rxjs/add/operator/map';
import {map} from 'rxjs/operators'; 


@Injectable()
/*({
  providedIn: 'root'
})*/
export class RegistrarUsuarioService {
  public url:string;
  public token:string;
  public identity:string;
 //RUTA DE ACCESO AL BACKEND REGISTRAR USUARIO
  constructor(private _http:Http) {
    this.url=GLOBAL.url;
   }

   //**********************REGISTRAR EL USUARIO*************************************
   register(usuario_registro){
    let json=JSON.stringify(usuario_registro);
    let params=json;
    //cabeceras .. tipo de contenido
    let headers=new Headers({'Content-Type':'application/json'});
    return this._http.post(this.url+'registrarusu', params, {headers:headers}).pipe(map(res => res.json())); 
   }
   //*******************FIN DE REGISTRAR EL USUARIO*************************************

}

