  calcularFactura() {
    if (this.productoventa.length >= 1) {
      this.venta.ven_iva = 0;
      this.venta.ven_subtotal = 0;
      this.venta.ven_descuento = 0;
      this.venta.ven_Totaldescuento = 0;
      this.venta.ven_Siniva = 0;
      this.venta.ven_total = 0;
      this.venta.ven_subtotalIva = 0;
      this.venta.ven_subtotalCero = 0;
      this.venta.ven_ice = 0;
      for (const producto of this.productoventa) {
        // console.log(producto);
        // en caso de tener que sacar el iva
        if (producto.iva == true) {
          // subtotal 12%
          this.venta.ven_subtotalIva = this.redondeoDecimales((this.venta.ven_subtotalIva + producto.subtotal));
          // iva 12%
          this.venta.ven_iva = this.redondeoDecimales((this.venta.ven_iva + producto.ivatotal));
        }
        // si no tiene iva
        if (producto.iva == false) {
          // subtotal 0%
          this.venta.ven_subtotalCero = this.redondeoDecimales((this.venta.ven_subtotalCero + producto.subtotal));
        }
        // subtotal sin impuestos
        this.venta.ven_subtotal = this.redondeoDecimales((this.venta.ven_subtotal + producto.subtotal));
        // descuentos
        if (producto.fk_descuento != '' && producto.fk_descuento != undefined && producto.fk_descuento != null) {
          this.venta.ven_descuento = this.redondeoDecimales((this.venta.ven_descuento + (producto.descuentoValor * producto.unidades)));
        }
        // ICE 0
        this.venta.ven_ice = 0;
        // valor total
        this.venta.ven_total = this.redondeoDecimales((this.venta.ven_total + producto.total));

        // this.venta.ven_Totaldescuento = this.venta.ven_Totaldescuento + producto.descuentoValor;
      }
    }
  }
