  // generar el numero de las facturas
  calcualrCodigo() {
    let codigo = 1;
    codigo = this.rangos[0].num_desde;
    //console.log(this.rangos);
    //console.log(this.rangos[0].num_desde);
    let unico = false;
    while (unico == false) {
      for (let i = 0; i < this.numeros.length; i++) {
        const numero = this.numeros[i].ven_codigo;
        if (numero == codigo) {
          codigo++;
        } else {
          unico = true;
          // console.log('codigo unico!');

        }
      }
    }
    var codigoString = codigo.toString();
    while (codigoString.length <= 3) {
      codigoString = '0' + codigoString;
    }
    this.venta.ven_codigo = codigoString;
    //console.log(codigo);
  }
