  cacularTotales() {
    // console.log(this.venta.fk_cliente);
    if (this.productoIngresar.prod_stock >= this.productoIngresar.unidades) {
      this.productoIngresar.stock_sig = parseInt(this.productoIngresar.prod_stock) - parseInt(this.productoIngresar.unidades);
      this.errorProducto = false;
      this.calcularTotalCaja();
      this.calcularFactura();
    } else {
      this.errorProducto = true;
      this.productoIngresar.total = 0;
    }
  }


