import { Component, OnInit } from '@angular/core';
import { RegistrarUsuarioService } from 'src/app/services/registrar-usuario.service';
import { Usuario } from 'src/app/models/usuario';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { promise } from 'protractor';
import { GLOBAL } from 'src/app/services/global';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { UploadService } from '../../../services/upload.service';

@Component({
  selector: 'app-actualizarusuario',
  templateUrl: './actualizarusuario.component.html',
  styleUrls: ['./actualizarusuario.component.css'],
  providers: [
    UsuarioServiceService,
    UploadService
  ],
})
export class ActualizarusuarioComponent implements OnInit {
  // RUTA DE ACCESO AL BACKEND
  // recoje los valores del usuario y los rellena en los campos
  constructor(private _actulizarusuario: UsuarioServiceService,
    private _router: Router,
    private _uploadService: UploadService,
    private _route:ActivatedRoute,
    private _UsuarioService: UsuarioServiceService
  ) {
    // localstored-identificar al usuario
    this.identity = this._actulizarusuario.getIdentity();
    this.token = this._actulizarusuario.getToken();
    this.actualizar_usuario = this.identity;
    this.url = GLOBAL.url;
    this.check = false;
  }
  public titulo: string;
  public actualizar_usuario: Usuario;
  public identity;
  public token;
  public alertMessage;
  public url: string;
  public image: string;
  public usuario: Usuario;
  public check: boolean;
  // SUBIR LA IMAGEN AL USUARIO
  public fileToUpload: Array<File>;

  ngOnInit() {

    console.log('usuario actualizar cargado');

  }
  sendFiles(usuario) {
    localStorage.setItem('identity', JSON.stringify(usuario));
    if (!this.fileToUpload) {
      console.log('no hay la imagen *inserte meme de lenin.jpg*');
    } else {
      this._uploadService.makeFileRequest(
        this.url + 'upload-imagen-user/' + this.actualizar_usuario._id,
        [],
        this.fileToUpload,
        this.token, 'image').then(
          (result: any) => {
            this.actualizar_usuario.usu_imagen = result.image;
            localStorage.setItem('identity', JSON.stringify(this.actualizar_usuario));
            console.log(this.actualizar_usuario);
          }
        );
    }
    this.alertMessage = 'El Usuario se ha Actualizado Con Exito';
  }

  public alertRegister;

  // ACTUALIZAR EL USUARIO
  onSubmitupdate() {
    console.log(this.actualizar_usuario);

    /*if(confirm("Desea Ingresar")){*/
    this._actulizarusuario.updateUsuario(this.actualizar_usuario).subscribe(
      response => {
        // actualizar completamente el objeto viejo por el nuevo que llega dede el api(backend)
        if (response.usuario) {
          this.sendFiles(this.actualizar_usuario);
          this.alertMessage = 'El Usuario se ha Actualizado';
          // localStorage.setItem('identity',JSON.stringify(this.usuario));
        } else {
          console.log(response);

          alert('Error al actulizar el usuario');
        }
        this.alertRegister = "Actualizado Correctamente"
        

      },
      error => {
        let errorMessage = error as any;
        if (errorMessage != null) {
          let body = JSON.parse(error._body);
          this.alertMessage = body.message;
          console.log('error al actualizar');
        }
      }
    );
    // alert('El Usuario se ha Actualizado Con Exito');
    /* }    */
  }

  fileChangeEvent(fileInput: any) {
    // recoje los archivos que se han seleccionado en los imput
    this.fileToUpload = fileInput.target.files as Array<File>;
    this.image = this.fileToUpload[0].name;
    console.log(this.fileToUpload);
    // console.log(this.actualizar_usuario.usu_imagen+'no hay imagen');
  }

  regresar() {
    this._router.navigate(['/usuario']);
  }
  comprobarContrase(pass) {
    this._UsuarioService.comprobarContrase(pass).subscribe(
      response => {
        console.log(response.Message);
        this.check = response.check;
        
      }
    )
  }

  cambiarContrase(contrasena){
    const id = this.actualizar_usuario._id;
    const actualizar = {
      usu_contrasena: contrasena
    }
    this._UsuarioService.cambiarContrase(id, actualizar).subscribe(
      response => {
        //console.log(response);
      }
    )
    // this._route.params.forEach((params:Params)=>{
    //   let id=params['id'];
    //   this._UsuarioService.cambiarContrase(id,this.actualizar_usuario).subscribe(
    //     response=>{
    //       if(response.actualizar_usuario){
    //         console.log('cambio correcto');
    //         console.log('nueva contrase;a',contrasena);
    //         this.usuario.usu_contrasena=contrasena;
            

    //       }
    //       else
    //       {
    //         console.log('cambio erroneo');
    //       }
    //     },
    //     error=>{
    //       console.log(error);
    //     }
    //   )
    // })
  }

  public editar_usuario;
  actualizar(){
    this._route.params.forEach((params:Params)=>{
      let id=params['id'];
      this._actulizarusuario.editarUsuario(id,this.actualizar_usuario).subscribe(
        response=>{
          if(response.actualizar_usuario){
            //console.log('exito');
              alert(this.actualizar_usuario)
          }
        },
        error=>{

        }
      )
    })
  }

}
