import { Component, OnInit } from '@angular/core';
import { RegistrarUsuarioService } from 'src/app/services/registrar-usuario.service';
import { Usuario } from 'src/app/models/usuario';
import { ActivatedRoute, Router } from '@angular/router';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { GLOBAL } from 'src/app/services/global';
import { UploadService } from '../../../services/upload.service';
import { count } from 'rxjs/operators';
import { Location } from '@angular/common';

@Component({
  selector: 'app-registrarse',
  templateUrl: './registrarse.component.html',
  providers: [
    RegistrarUsuarioService,
    UploadService
  ],
  styleUrls: ['./registrarse.component.css']
})

export class RegistrarseComponent implements OnInit {
  public usuario_registro: Usuario;
  public tituloRegistro;
  public token;
  public identity;
  public url: string;
  public image: string;
  
  public alertRegister;

  public listar: Usuario;

  // SUBIR LA IMAGEN AL USUARIO
  public fileToUpload: Array<File>;
  public today =new Date;
  constructor(
    private _registrarservicio: RegistrarUsuarioService,
    private _route: ActivatedRoute,
    private _router: Router,
    private _usuarioServicio: UsuarioServiceService,
    private _uploadService: UploadService,
    private _location:Location,
  ) {
    this.usuario_registro = new Usuario('', '', '', '', '', '', '','', '', '', '', 'ROL_USER', '', '');
    this.tituloRegistro = 'Guardar Registro';
    this.identity = this._usuarioServicio.getIdentity();
    this.token = this._usuarioServicio.getToken();
    this.url = GLOBAL.url;
    this.getToday();

  }
  limpiar(form) {
    form.reset();
    this.usuario_registro = new Usuario('', '', '', '', '', '', '','', '', '', '', 'ROL_USER', '', '');
    this.cedulaCorrecta = null;
  }
  getToday() {
    this.today.getDate();
  }
public cedulaCorrecta:boolean;
  verifyCedula(cedula) {
    this.cedulaCorrecta = false;
    if (cedula.length === 10) {
      let cedulaCache: any[]; let sumaPares = 0;
      let sumaImpares = 0;let sumaTotal: number;
      let verificador: number;let comparador = 10;
      cedulaCache = cedula.split('');
      for (let i = 0; i < 9; i =  i + 2) {
        let impar = cedulaCache[i];
        impar = impar * 2;
        if (impar >= 10) {
          impar = impar - 9;
        }
        sumaImpares = sumaImpares + impar;
      }
      for (let i = 1; i < 8; i = i + 2) {
        const par = cedulaCache[i];
        sumaPares = sumaPares + parseInt(par, 0);
      }
      sumaTotal = sumaPares + sumaImpares;
      while (comparador < sumaTotal) {
        comparador = comparador + 10;
      }
      if (comparador - sumaTotal <= 9) {
        verificador = comparador - sumaTotal;
        if (verificador == cedulaCache[9]) {
          this.cedulaCorrecta = true;
        } else {
          this.cedulaCorrecta = false;
        }
      }
    }
  }
  onSubmitRegister(form) {
    this.today = new Date;
    
      this.usuario_registro.usu_fecha=this.today.toString();
      //console.log(this.usuario_registro);
      this._registrarservicio.register(this.usuario_registro).subscribe(
        response => {
          if (response.usuario) {
            this.sendFiles(response.usuario);
            this.usuario_registro=response.usuario;
            const descripcion=this.usuario_registro.usu_nombre;
            this.usuario_registro = new Usuario('', '', '', '', '', '','', '', '', '', '', 'ROL_USER', '', '');
            this.alertRegister = 'El Usuario : ' + descripcion + ' ha sido creado exitosamente';
            this.getMostrar();
           form.reset();
           this.cedulaCorrecta = null;
           setTimeout(() => {
            this.alertRegister = null;
          }, 3000);
          } else {
            alert('error al regitrar');
            alert(this.alertRegister);
          }
        },
        error => {
          const errorMessage = error as any;
          if (errorMessage != null) {
            const body = JSON.parse(error._body);
            this.alertRegister = body.message;
            // console.log(error);
          }
          // alert(this.alertRegister);
          this.usuario_registro = new Usuario('', '', '', '', '', '', '', '','', '', '', 'ROL_USER', '', '');
          setTimeout(() => {
            this.alertRegister = null;
            this.limpiar(form);
          }, 3000);
        }
      );
    
  }

  regresar() {
    this._location.back();
  }

  ngOnInit() {
    this.getMostrar();
  }
  getMostrar() {
    this._usuarioServicio.getUsuario(this.token).subscribe(

      response => {
        // console.log('la respuesta no hayt');
        if (!response.usuario) {
          // console.log('error no hay nada');
        } else {
          this.listar = response.usuario;
          // console.log(this.listar);
        }
      },
      error => {
        console.log(error as any);
      }
    );
  }
  sendFiles(usuario) {
    if (!this.fileToUpload) {
      //console.log('no hay la imagen');
    } else {
      this._uploadService.makeFileRequest(
        this.url + 'upload-imagen-user/' + usuario._id,
        [],
        this.fileToUpload,
        this.token, 'image').then(
          (result: any) => {
            //console.log('imagen subida', result);
          }
        );
    }
  }
  fileChangeEvent(fileInput: any) {
    // recoje los archivos que se han seleccionado en los imput
    this.fileToUpload = fileInput.target.files as Array<File>;
    this.image = this.fileToUpload[0].name;
    //console.log(this.fileToUpload);
    // console.log(this.actualizar_usuario.usu_imagen+'no hay imagen');
  }

}
