import { Component, OnInit } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { Proveedor } from 'src/app/models/proveedor';
import { ProveedorService } from 'src/app/services/proveedor.service';
import { Router } from '@angular/router';
import { GLOBAL } from 'src/app/services/global';
import { Location } from '@angular/common';

@Component({
  selector: 'app-proveedor',
  templateUrl: './proveedor.component.html',
  styleUrls: ['./proveedor.component.css']
})
export class ProveedorComponent implements OnInit {
  public token;
  public identity;
  public url: string;
  public alertRegister;
  public proveedor_guardar: Proveedor;
  public proveedor_mostrar: any[]=[];
  public bufferProveedor:any[]=[];
  public today = new Date;
  public cedulaCorrecta: boolean;
  public verProveedor: Proveedor;

  constructor(
    private _usuarioServicio: UsuarioServiceService,
    private _proveedorServicio: ProveedorService,
    private _router: Router,
    private _location:Location,
  ) {
    this.identity = this._usuarioServicio.getIdentity();
    this.token = this._usuarioServicio.getToken();
    this.url = GLOBAL.url;
    this.objetoProveedor();
    this.getToday();

  }
  regresar(){
    this._location.back();
  }
  limpiar(form) {
    form.reset();

    this.objetoProveedor();

    this.numeroCorrecto = true;
    this.cedulaCorrecta = null;
  }
  public numeroCorrecto: boolean;

  public countFindeds: number;
  buscarProveedor(termino){
    termino=termino.toLowerCase();
    this.proveedor_mostrar=[],[];
    this.countFindeds = 0;
    for (const proveedor of this.bufferProveedor) {
      let nombre=proveedor.prov_cedula;
      nombre=nombre+proveedor.prov_apellido + proveedor.prov_nombre;
      nombre=nombre+proveedor.prov_nombre +proveedor.prov_apellido ;
      nombre=nombre.toLowerCase();
      if(!proveedor.prov_cedula){
        nombre="0"+nombre;
      }
      if( nombre.indexOf(termino)>=0){
        this.countFindeds++;
        this.proveedor_mostrar.push(proveedor);
      }
    }
}
mensajeCedula(cedula): string {
  let mensaje = 'Cedula';
  if (cedula != null && cedula != '') {
    if (cedula != '' && cedula.length <= 10) mensaje = 'Cedula Correcta!.';
    if (cedula.length > 10) mensaje = 'RUC Correcto!.';
  }
  return mensaje
}
  verifyCedula(cedula) {
    this.cedulaCorrecta = false;
    if (cedula.length == 10 || cedula.length == 13) {
      let cedulaCache: any[];
      let sumaPares = 0;
      let sumaImpares = 0;
      let sumaTotal: number;
      let verificador: number;
      let comparador = 10;
      cedulaCache = cedula.split('');
      for (let i = 0; i < 9; i =  i + 2) {
        let impar = cedulaCache[i];
        impar = impar * 2;
        if (impar >= 10) {
          impar = impar - 9;
        }
        sumaImpares = sumaImpares + impar;
      }
      for (let i = 1; i < 8; i = i + 2) {
        const par = cedulaCache[i];
        sumaPares = sumaPares + parseInt(par, 0);
      }
      sumaTotal = sumaPares + sumaImpares;
      while (comparador < sumaTotal) {
        comparador = comparador + 10;
      }
      if (comparador - sumaTotal <= 9) {
        verificador = comparador - sumaTotal;
        if (verificador == cedulaCache[9]) {
          this.cedulaCorrecta = true;
        } else {
          this.cedulaCorrecta = false;
        }
      }
    }
  }
  getToday() {
    this.today.getDate();
  }
  objetoProveedor(){
    this.proveedor_guardar = new Proveedor('','','','', '', '', '', '', '', '', '', '','','');

  }
  ngOnInit() {
    this.getPro();
  }
  registrar(form) {
    // if (confirm('Desea Guardar')) {
      this.proveedor_guardar.fk_usuario=this.identity;
      this.today = new Date;

      this.proveedor_guardar.prov_fecha=this.today.toString();
      this._proveedorServicio.addProveedor(this.proveedor_guardar).subscribe(
        response => {
          if (response.proveedor) {
            console.log('no se ha guardado');
            this.proveedor_guardar = response.proveedor;

            const Descripcion = this.proveedor_guardar.prov_nombre;
            const des = this.proveedor_guardar.prov_apellido;
            this.alertRegister = 'La Marca: ' + Descripcion +'  '+ des + '  , se ha registrado Correctamente!..';
            this.objetoProveedor();
            this.getPro();
            form.reset();
            this.cedulaCorrecta=null;
            setTimeout(() => {
              this.alertRegister = null;
            }, 3000);
          }
          else {
            this.objetoProveedor();
            console.log('error');
            
          }
           this.getPro();
        },
        error => {
          const errorMessage = error as any;
          if (errorMessage != null) {
            const body = JSON.parse(error._body);
            this.alertRegister = body.message;
          }
          this.objetoProveedor();
          setTimeout(() => {
            this.alertRegister = null;
            this.limpiar(form);
          }, 3000);

        }
      )
    // }
    this.objetoProveedor();

  }

  getPro() {
    this._proveedorServicio.getProveedor(this.token).subscribe(
      response => {
        if (!response.proveedor_mostrar) {
          this.proveedor_mostrar = response.proveedor;
          this.bufferProveedor=response.proveedor;
        }
        else {
          console.log('error no se encuentran los datos');

        }
      },
      error => {
        console.log(<any>error);
      }
    )
  }

  delete(id) {
    this._proveedorServicio.deleteProveedor(id).subscribe(
      response => {
        if (!response.no) {
          console.log('eliminado');
        }
        else {
          console.log('Error al Eliminar');
          alert('Error al Eliminar');
        }
        this.getPro();
        this.alertRegister = " ";
      },
      error => {
        var errorMessage = <any>error;
        if (errorMessage != null) {
          var body = JSON.parse(error._body)
          this.alertRegister = body.message;
          console.log(error);
        }
      }
    )
  }


}
