import { Injectable } from '@angular/core';
import { GLOBAL } from './global';
import { Headers, Http } from '@angular/http';
import 'rxjs/add/operator/map';
import {map} from 'rxjs/operators'; 

@Injectable({
  providedIn: 'root'
})
export class DetalleCompraService {
  public url: string;
  constructor(
    private _http: Http
  ) {
    this.url = GLOBAL.url;
  }

  guardarDetalleCompra(detalleCompra) {
    const token = localStorage.getItem('token');
    const params = JSON.stringify(detalleCompra);
    const headers = new Headers({
      'Content-Type':'application/json', 
      'Authorization':token
    });
    return this._http.post(this.url + 'guardar-detalle-compra/', params, {headers: headers}).pipe(map(res => res.json()))
  }
  getdetallecompra(){
    const token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'mostrar-detalle-compra', { headers: headers }).map(res => res.json());
  }


  mostrarUnCompra(_id: string) {
    let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url+'get-detallecompra/'+_id,{headers:headers}).map(res=>res.json());
  }
}


