import { Component, OnInit } from '@angular/core';
import { Producto } from 'src/app/models/producto';
import { RegistrarseComponent } from '../usuario/registrarse/registrarse.component';
import { ActivatedRoute, Router } from '@angular/router';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { GLOBAL } from 'src/app/services/global';

@Component({
  selector: 'app-producto',
  templateUrl: './producto.component.html',
  styleUrls: ['./producto.component.css']
})
export class ProductoComponent implements OnInit {
  public titulo: string;
  public producto_registro: Producto;
  public productoArray: Producto[];
  public identity;
  public token;
  public url: string;

  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _usuarioservicio: UsuarioServiceService,
  ) {
    this.titulo = "Producto";
    this.identity = this._usuarioservicio.getIdentity();
    this.token = this._usuarioservicio.getToken();
    this.url = GLOBAL.url;

  }
  ngOnInit() {

  }
}
