import { Injectable } from "@angular/core";
import { Http, Headers } from '@angular/http';
import { GLOBAL } from './global';
import 'rxjs/add/operator/map';
import {map} from 'rxjs/operators'; 


@Injectable()

export class StatisticsService {
    public url: string;
    constructor(
        private _http: Http
    ) {
        this.url = GLOBAL.url;
    }

    getUserStatistics() {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        return this._http.get(this.url + 'get-conteos', { headers: headers }).pipe(map(res => res.json()));
    }
    getNumerosCompras(id) {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        return this._http.get(this.url + 'get-numeros-compras/'+id, { headers: headers }).pipe(map(res => res.json()));
    }
    getTotalgastoCompra(){
        let headers = new Headers({ 'Content-Type': 'application/json' });
        return this._http.get(this.url + 'get-costos-compras/', { headers: headers }).pipe(map(res => res.json()));
    }
    getTotalStock(){
        let headers = new Headers({ 'Content-Type': 'application/json' });
        return this._http.get(this.url + 'get-stock/', { headers: headers }).pipe(map(res => res.json()));
    }
    getTotalVenta(){
        let headers = new Headers({ 'Content-Type': 'application/json' });
        return this._http.get(this.url + 'getNumeroVentas/', { headers: headers }).pipe(map(res => res.json()));
    }

    cedulaCliente(){
        let headers = new Headers({ 'Content-Type': 'application/json' });
        return this._http.get(this.url + 'get-cedulas/', { headers: headers }).pipe(map(res => res.json()));
    }

// productos que se encuentran en el detalle
    prod() {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        return this._http.get(this.url + 'get-productos', { headers: headers }).pipe(map(res => res.json()));
    }

}
