import { Component, OnInit } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { Params, ActivatedRoute } from '@angular/router';
import { DetalleCompraService } from 'src/app/services/detalle-compra.service';
import { CompraService } from 'src/app/services/compra.service';
import { GLOBAL } from 'src/app/services/global';
import { detalleCompra } from 'src/app/models/detalleCompra';

@Component({
  selector: 'app-detalle-compra',
  templateUrl: './detalle-compra.component.html',
  styleUrls: ['./detalle-compra.component.css'],
  providers:[UsuarioServiceService,CompraService,DetalleCompraService]

})
export class DetalleCompraComponent implements OnInit {
  public identity;
  public token;
  public url:string;
  public mostrardetalle:detalleCompra[];
  public compra: any;
  constructor(
    private _usuarioService:UsuarioServiceService,
    private _route:ActivatedRoute,
    private _serviceDetalleCompra:DetalleCompraService,
    private _compraService: CompraService
  ) { 
    this.identity=this._usuarioService.getIdentity();
    this.token=this._usuarioService.getToken();
    // this.getmostrarcompra();
    this.url=GLOBAL.url;
  }
  ngOnInit() {
    this.getmostrarcompra();
  }

  imprSelec(historial) {
    // let ventana=window.open('', 'PRINT');
    // ventana.print();
    let ficha = document.getElementById(historial);
    let ventimp = window.open(' ', 'popimpr');
    ventimp.document.write(ficha.innerHTML);
    // ventimp.document.close();
    ventimp.print();
    ventimp.close();
  }
  

getmostrarcompra() {
  this._route.params.forEach((params: Params) => {
    if (params['id'] != undefined) {
      let id = params['id'];
      this.loadDetalle(id);
    }
  })
}
onPrint(){
  window.print();
}   
loadDetalle(id) {
  this._serviceDetalleCompra.mostrarUnCompra(id).subscribe(
    response => {
      if (response.compra) {
        this.mostrardetalle = response.compra;
        console.log(this.mostrardetalle);
        //console.log('respuesta del servidor',response);
      }
      else{
        console.log('no se puede mostrar');
      }
    },
    error => {
      var errorMessage = <any>error;
      if (errorMessage != null) {
        var body = JSON.parse(error._body)
        console.log(error);
      }
    }
  )
}

}
