import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { GLOBAL } from 'src/app/services/global';
import { Categoria } from 'src/app/models/categoria';
import { CategoriaService } from 'src/app/services/categoria.service';

import * as jsPDF from 'jspdf';
import { Location } from '@angular/common';

@Component({
  selector: 'app-crear-categoria',
  templateUrl: './crear-categoria.component.html',
  styleUrls: ['./crear-categoria.component.css'],
  providers: [UsuarioServiceService, CategoriaService]
})
export class CrearCategoriaComponent implements OnInit {
  public titulo: string;
  /**Variable de categortia principal */
  public categoria_registrar: Categoria;
  public identity;
  public token;
  public url: string;
  public alertRegister;
  public numbers = new Array(10);
  public listar_caegoria: any[]=[];
  public bufferCategoria:any[]=[];
  public today = new Date;

  public verCategoria:Categoria;

  constructor(
    private _usuarioservicio: UsuarioServiceService,
    private _categoriaServicio: CategoriaService,
    private _router: Router,
    private _listar_categoria: CategoriaService,
    private _location:Location,
  ) {
    this.titulo = "Crear Nueva Categoria";
    this.identity = this._usuarioservicio.getIdentity();
    this.token = this._usuarioservicio.getToken();
    this.url = GLOBAL.url;
    this.getToday();
    this.iniciarCategoria();
    this.codigoAletorio();
  }

  public nombre;
public cadenas;
  codigoAletorio(){
    let chars='0123456789';
    let size='5';
    let cadena='';
    for( let i=0;i<5;i++){
      cadena+=chars[Math.floor(Math.random()*(chars.length))];
    }
    this.nombre="CAT"
    this.cadenas=cadena;
    this.categoria_registrar.cat_nombre=this.nombre+this.cadenas;
    
  }

  detalle() {
    var id = document.getElementById("detalleCategoria");

    // var imgData='bjdjt-puacn.webp'
    var pdf = new jsPDF({
      orientacion: '3',
      unit: 'pt',
      format: 'A4',
      posicicion: 3,
    });
    pdf.setFont('times')    //tipo de letra  
    pdf.setFontSize(22);      //tama de letra

    // pdf.addImage(imgData,'JPEG', 15, 40, 18, 16)

    pdf.setTextColor(84, 201, 47)   //color RGB
    pdf.text("Papeleria & Bazar - 'San Francisco' ", 150, 50); //posicion de la letra columna fila

    pdf.setLineWidth(3) //grueso de la linea
    pdf.setDrawColor(251, 152, 4)   // color d ela linea
    pdf.line(80, 67, 525, 67) //  posicion de la linea columna, fila, columna, fila

    // pdf.setTableHeaderRow([1500, 7000]);
    // pdf.table(120, 120, 120, [],120)
    // pdf.table(100, 100,100,[],100)

    pdf.setLineWidth(0.3)
    pdf.setDrawColor(150, 132, 104)
    pdf.setTextColor(7, 8, 6) //color del texto


    // pdf.setTableHeaderRow([10000,5522222])
    pdf.fromHTML(id, 80, 75); //todos los datos de mi tabla  Marca HTML

    pdf.setLineWidth(3) //grueso de la linea
    pdf.setDrawColor(251, 152, 4)   // color d ela linea
    pdf.line(80, 750, 525, 750) //  posicion de la linea columna, fila, columna, fila

    pdf.setFontSize(12);

    pdf.text('Emitido por : ' + this.identity.usu_apellido + ' ' + this.identity.usu_nombre, 80, 775);
        pdf.text('Fecha           : ' + this.today.toLocaleDateString() + "  "+ this.today.getHours()+ ":" + this.today.getMinutes() + ":"+this.today.getSeconds(), 80, 790);

    pdf.save("Detalle-Categoria.pdf");
  }


  onPrint(){
    window.print();
}
  public countFindeds: number;
  buscarCategoria(termino){
    termino=termino.toLowerCase().replace(/ /g, '');
    this.listar_caegoria=[],[];
    this.countFindeds = 0;
    for (const categoria of this.bufferCategoria) {
      let nombre=categoria.cat_nombre;
      nombre=nombre+categoria.cat_descripcion;
      nombre=nombre.toLowerCase();
      if(!categoria.cat_nombre){
        nombre="0"+nombre;
      }
      if( nombre.indexOf(termino)>=0){
        this.countFindeds++;
        this.listar_caegoria.push(categoria);
      }
    }
}
  getToday() {
    this.today.getDate();
  }
  
  iniciarCategoria(){
    this.categoria_registrar = new Categoria('', '', '', '','','','');
  }

  contador(){
    for (let i = 0; i < this.listar_caegoria.length; i++) {
      const element = this.listar_caegoria[i];
      element.index=i+1;
    }
  }

  getMostrarCategoria() {

    this._listar_categoria.getCategoria(this.token).subscribe(

      response => {
        if (!response.listar_caegoria) {
          this.listar_caegoria = response.categoria;
          this.bufferCategoria=response.categoria;
          this.contador();
        }
        else {
        }
      },
      error => {
        console.log(<any>error);
      }
    )

  }

  ngOnInit() {
    this.getMostrarCategoria();
    

  }
  limpiar(form){
    this.categoria_registrar.cat_descripcion='';
    this.alertRegister = '';
  }

  //*******************REGISTRAR CATEGORIA*************** */
  /**Funcion Registro Categoria
   * @param id
   */
  registrarCategoriaaaa(form) {
    // if (confirm('Desea Guardar')) {
      this.categoria_registrar.fk_usuario=this.identity;
     this.today = new Date;
      
      this.categoria_registrar.cat_fecha=this.today.toString();
      this._categoriaServicio.addCategoria(this.token, this.categoria_registrar).subscribe(
        response => {
          if (response.categoria) {
            this.categoria_registrar = response.categoria;
            const catDescripcion = this.categoria_registrar.cat_descripcion;
            this.iniciarCategoria();
            // alert("Exito al Guardar");
            // form.reset();
            this.alertRegister = 'La Categoria: ' + catDescripcion + ', se ha registrado Correctamente!..';
            // this.limpiar();
            form.reset();
            setTimeout(() => {
              this.alertRegister = null;
            }, 3000);
          } 
          this.getMostrarCategoria();
          // this.limpiar();
          this.codigoAletorio();

        },
        error => {
          const errorMessage = error as any;
          if (errorMessage != null) {
            const body = JSON.parse(error._body);
            this.alertRegister = body.message;
            
          }
          setTimeout(() => {
            this.alertRegister = null;
            this.limpiar(form);
          }, 3000);
          // alert(this.alertRegister);
          this.iniciarCategoria();
          this.codigoAletorio();
        }
      )
      this.iniciarCategoria();
    // }
  }
  //*******************FIN DE REGISTRAR CATEGORIA*************** */

  regresar() {
    this._location.back();
  }


  delete(id) {
    if (confirm('Desea Eliminar')) {
      this._categoriaServicio.deleteCategoria(id).subscribe(
        response => {
          if (!response.categoria) {
            alert('Error en el servidor');
          }
          this.getMostrarCategoria();
        },
        error => {
          console.log(<any>error);
        }
      )
    }
  }

  atras() {
    this._router.navigate(['categoria']);
  }
}



