import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { GLOBAL } from 'src/app/services/global';
import { Categoria } from 'src/app/models/categoria';
import { CategoriaService } from 'src/app/services/categoria.service';

import * as jsPDF from 'jspdf';
import { StatisticsService } from 'src/app/services/statistics.service';

@Component({
  selector: 'app-listar-categoria',
  templateUrl: './listar-categoria.component.html',
  styleUrls: ['./listar-categoria.component.css'],
  providers: [
    CategoriaService,
    StatisticsService
  ]
})
export class ListarCategoriaComponent implements OnInit {
  public titulo; identity;
  public token;
  public url
  public numbers = new Array(10);
  public listar_caegoria: any[];
  public countFindeds: number;
  public bufferCategoria: any[] = [];

  constructor(

    private _router: Router,
    private _listar_categoria: CategoriaService,
    private _usuarioservicio: UsuarioServiceService,
    private _statisticsService:StatisticsService,
  ) {
    this.asc=true;
    this.identity = this._usuarioservicio.getIdentity();
    this.token = this._usuarioservicio.getToken();
    this.url = GLOBAL.url;
    this.estadisticas();
  }
  onPrint() {
    window.print();
  }
  public statistics
  estadisticas(){
    this._statisticsService.getUserStatistics().subscribe(
      response => {
        this.statistics = response.conteo;
        //console.log(this.statistics);
        
      }
    );
  }


  
  detalle() {
    var id = document.getElementById("detalleCategoria");

    // var imgData='bjdjt-puacn.webp'
    var pdf = new jsPDF({
      orientacion: '3',
      unit: 'pt',
      format: 'A4',
      posicicion: 3,
    });
    pdf.setFont('times')    //tipo de letra  
    pdf.setFontSize(22);      //tama de letra

    // pdf.addImage(imgData,'JPEG', 15, 40, 18, 16)

    pdf.setTextColor(84, 201, 47)   //color RGB
    pdf.text("Papeleria & Bazar - 'San Francisco' ", 150, 50); //posicion de la letra columna fila

    pdf.setLineWidth(3) //grueso de la linea
    pdf.setDrawColor(251, 152, 4)   // color d ela linea
    pdf.line(80, 67, 525, 67) //  posicion de la linea columna, fila, columna, fila

    pdf.setFontSize(13);      //tama de letra
    pdf.setTextColor(0,0,0)// color de la letra
    pdf.text("Detalle de la Categoria ", 270, 85); //posicion de la letra columna fila

    // pdf.setTableHeaderRow([1500, 7000]);
    // pdf.table(120, 120, 120, [],120)
    // pdf.table(100, 100,100,[],100)

    pdf.setLineWidth(0.3)
    pdf.setDrawColor(150, 132, 104)
    pdf.setTextColor(7, 8, 6) //color del texto


    // pdf.setTableHeaderRow([10000,5522222])
    pdf.fromHTML(id, 80, 85); //todos los datos de mi tabla  Marca HTML

    pdf.setLineWidth(3) //grueso de la linea
    pdf.setDrawColor(251, 152, 4)   // color d ela linea
    pdf.line(80, 750, 525, 750) //  posicion de la linea columna, fila, columna, fila

    pdf.setFontSize(12);

    pdf.text('Emitido por : ' + this.identity.usu_apellido + ' ' + this.identity.usu_nombre, 80, 775);
    pdf.text('Fecha           : ' + this.today.toLocaleDateString() + "  "+ this.today.getHours()+ ":" + this.today.getMinutes() + ":"+this.today.getSeconds(), 80, 790);

    pdf.save("Detalle-Categoria.pdf");
  }


  catpdf() {
    var id = document.getElementById("tabMensaje");
    // var imgData='bjdjt-puacn.webp'
    var pdf = new jsPDF({
      orientacion: '3',
      unit: 'pt',
      format: 'A4',
      posicicion: 3,
    });
    pdf.setFont('times')    //tipo de letra  
    pdf.setFontSize(22);      //tama de letra

    // pdf.addImage(imgData,'JPEG', 15, 40, 18, 16)
    pdf.setTextColor(84, 201, 47)   //color RGB
    pdf.text("Papeleria & Bazar - 'San Francisco' ", 150, 50); //posicion de la letra columna fila

    pdf.setLineWidth(3) //grueso de la linea
    pdf.setDrawColor(251, 152, 4)   // color d ela linea
    pdf.line(80, 67, 525, 67) //  posicion de la linea columna, fila, columna, fila

    pdf.setFontSize(13);      //tama de letra
    pdf.setTextColor(0,0,0)// color de la letra
    pdf.text("Reporte - Registro de Categorias ", 240, 85); //posicion de la letra columna fila

    // pdf.setTableHeaderRow([1500, 7000]);
    // pdf.table(120, 120, 120, [],120)
    // pdf.table(100, 100,100,[],100)

    pdf.setLineWidth(0.3)
    pdf.setDrawColor(150, 132, 104)
    pdf.setTextColor(7, 8, 6) //color del texto


    // pdf.setTableHeaderRow([10000,5522222])
    pdf.fromHTML(id, 80, 85); //todos los datos de mi tabla  Marca HTML

    pdf.setLineWidth(3) //grueso de la linea
    pdf.setDrawColor(251, 152, 4)   // color d ela linea
    pdf.line(80, 750, 525, 750) //  posicion de la linea columna, fila, columna, fila

    pdf.setFontSize(12);

    pdf.text('Emitido por : ' + this.identity.usu_apellido + ' ' + this.identity.usu_nombre, 80, 775);
    //     pdf.text('Fecha           : ' + this.today.toLocaleDateString() + "  "+ this.today.getHours()+ ":" + this.today.getMinutes() + ":"+this.today.getSeconds(), 80, 790);
    pdf.text('Fecha           : ' + this.today.toLocaleDateString() + "  "+ this.today.getHours()+ ":" + this.today.getMinutes() + ":"+this.today.getSeconds(), 80, 790);

    pdf.save("Reporte-Categoria.pdf");
  }

  public today = new Date;

  buscarCategoria(termino) {
    termino = termino.toLowerCase().replace(/ /g, '');
    this.listar_caegoria = [], [];
    this.countFindeds = 0;
    for (const categoria of this.bufferCategoria) {
      let nombre = categoria.cat_nombre ;
      nombre = nombre + categoria.cat_descripcion;
      nombre = nombre + categoria.cat_fecha;
      nombre = nombre.toLowerCase().replace(/ /g, '');;
      if (!categoria.cat_nombre) {
        nombre = "0" + nombre;
      }
      if (nombre.indexOf(termino) >= 0) {
        this.countFindeds++;
        this.listar_caegoria.push(categoria);
      }
    }
  }

  mostrarCategoria(){
    this._listar_categoria.getCategoria(this.token).subscribe(
      response => {
        if (response.categoria) {
          this.listar_caegoria = response.categoria;
          this.bufferCategoria = response.categoria;
          this.reordenarArray('fecha');

          //console.log(this.listar_caegoria);
          
        }
        else {
        }
      },
      error => {
        console.log(<any>error);
        console.log( 'no se puede mostrar');
        
      }
    )
  }
  ngOnInit() {
    this.mostrarCategoria();
  }


  
  public asc:boolean;
  cambiarOrden(celda) {
    if (this.asc == true) this.asc = false; else
      if (this.asc == false) this.asc = true;
    this.reordenarArray(celda);
  }

  
  reordenarArray(celda){
    if(this.asc==true){
      if(celda=='codigo'){
        this.listar_caegoria.sort((menor, mayor) => (menor.cat_nombre.localeCompare(mayor.cat_nombre)));
      }else if(celda=='descripcion'){
        // this.mostrar_marca.sort((menor, mayor) => (parseInt(mayor.prod_stock) - parseInt(menor.prod_stock)));
        this.listar_caegoria.sort((menor, mayor) => (menor.cat_descripcion.localeCompare(mayor.cat_descripcion)));

      }else if(celda=='fecha'){
        this.listar_caegoria.sort((menor,mayor)=>{
          const a = new Date(menor.cat_fecha);
          const b = new Date(mayor.cat_fecha);
          if (a == b) return 0
          if (a > b) return -1
          if (a < b) return 1
        })
        
      }
    } else if(this.asc==false){
      if(celda=='codigo'){
        this.listar_caegoria.sort((menor, mayor) => (mayor.cat_nombre.localeCompare(menor.cat_nombre)));
      }else if(celda=='descripcion'){
        // this.mostrar_marca.sort((menor, mayor) => (parseInt(menor.prod_stock) - parseInt(mayor.prod_stock)));
        this.listar_caegoria.sort((menor, mayor) => (mayor.cat_descripcion.localeCompare(menor.cat_descripcion)));

      }else if(celda=='fecha'){
        this.listar_caegoria.sort((menor,mayor)=>{
          const a = new Date(menor.cat_fecha);
          const b = new Date(mayor.cat_fecha);
          if (a == b) return 0
          if (a > b) return 1
          if (a < b) return -1
        })
        
      }
    }
    for (let i = 0; i < this.listar_caegoria.length; i++) {
      const element = this.listar_caegoria[i];
      element.index=i+1;
      //console.log(element);
      
    }
  }





}
