import { Injectable } from '@angular/core';
import { Http,Headers, RequestOptions } from '@angular/http';
import { GLOBAL } from './global';
import 'rxjs/add/operator/map';
import {map} from 'rxjs/operators'; 
import { Categoria } from '../models/categoria';
import { Observable } from 'rxjs';

@Injectable()

export class CategoriaService {
  public url:string;
  constructor(private _http:Http) { 
  this.url=GLOBAL.url;  
  }

 //*******************si vale REGISTRAR CATEGORIA*************** */
 addCategoria(token,categoria_registrar:Categoria){
    let params=JSON.stringify(categoria_registrar)
    let headers=new Headers({'Content-Type':'application/json', 'Authorization':token});
    return this._http.post(this.url+'guardarcategoria',params, {headers:headers}).pipe(map(res=>res.json()));
  }
//**************** FIN DE REGISTRAR CATEGORIA*************** */


getCategoria(token){
  let headers=new Headers({'Content-Type':'application/json', 'Authorization':token}); 
  let opcion=new RequestOptions({headers:headers});
  return this._http.get(this.url+'mostrarcategoria',opcion).map(res=>res.json());
}

//*
//******************MOSTRAR 1 CATEGORIA *************** */

mostrarUnaCategoria(id:String){  
  let token = (localStorage.getItem('token')); 
  let headers=new Headers({'Content-Type':'application/json', 'Authorization':token}); 
  //let opcion=new RequestOptions({headers:headers});
  return this._http.get(this.url+'categoria/'+id,{headers:headers}).pipe(map(res=>res.json()));
}
  //*****************FIN DE MOSTRAR 1 CATEGORIA************** */

editCategoria(id:string,categoria_editar: Categoria){
  let token =(localStorage.getItem('token'));
  let params=JSON.stringify(categoria_editar);
  let headers=new Headers({'Content-Type':'application/json', 'Authorization':token}); 
  return this._http.put(this.url+'actualizarcategoria/'+id, params,{headers:headers}).pipe(map(res=>res.json()));
}
//****************** ELIMINAR CATEGORIA *************** */
  deleteCategoria(id:string){
    let token=localStorage.getItem('token');
    let headers=new Headers({'Content-Type':'application/json', 'Authorization':token});
    let opcion=new RequestOptions({headers:headers});
    return this._http.delete(this.url+'deletecategoria/'+id,opcion).pipe(map(res=>res.json()));
  }
//*******************ELIMINAR CATEGORIA*************** */

} 
