import { Component, OnInit, DoCheck } from '@angular/core';
import { Cliente } from 'src/app/models/cliente';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { Router } from '@angular/router';
import { ClienteService } from 'src/app/services/cliente.service';
import { GLOBAL } from 'src/app/services/global';
import { StatisticsService } from 'src/app/services/statistics.service';
import { Location } from '@angular/common';

@Component({
  selector: 'app-cliente',
  templateUrl: './cliente.component.html',
  styleUrls: ['./cliente.component.css'],
  providers: [StatisticsService, UsuarioServiceService]
})
export class ClienteComponent implements OnInit {
  public cliente: Cliente;
  public token;
  public identity;
  public url: string;
  public alertRegister;
  public cliente_guardar: Cliente;
  public cliente_mostrar: any[] = [];
  public bufferCiente: any[] = [];
  public desplegar;
  public today = new Date;
  public cedulaCorrecta: boolean;
  public verCliente: Cliente;

  constructor(
    private _usuarioServicio: UsuarioServiceService,
    private _router: Router,
    private _clienteServicio: ClienteService,
    private _statisticsService: StatisticsService,
    private _location:Location,

  ) {
    this.identity = this._usuarioServicio.getIdentity();
    this.token = this._usuarioServicio.getToken();
    this.url = GLOBAL.url;
    this.mostrarCliente();
    this.objetoCliente();
    this.desplegar = false;
  }
  descuentos() {

  }
  regresar(){
    this._location.back();
  }
  public countFindeds: number;
  buscasCliente(termino) {
    termino = termino.toLowerCase().replace(/ /g, '');
    this.cliente_mostrar = [], [];
    this.countFindeds = 0;
    for (const cliente of this.bufferCiente) {
      let nombre = cliente.cli_nombre + cliente.cli_apellido;
      nombre = nombre + cliente.cli_cedula;
      nombre = nombre + cliente.cli_nombre + cliente.cli_apellido;
      nombre = nombre + cliente.cli_apellido + cliente.cli_nombre;
      nombre = nombre.toLowerCase().replace(/ /g, '');
      if (!cliente.cli_cedula) {
        nombre = "0" + nombre;
      }
      if (nombre.indexOf(termino) >= 0) {
        this.countFindeds++;
        this.cliente_mostrar.push(cliente);
      }
    }
  }


  getToday() {
    this.today.getDate();
  }
  mensajeCedula(cedula): string {
    let mensaje = 'Cedula';
    if (cedula != null && cedula != '') {
      if (cedula != '' && cedula.length <= 10) mensaje = 'Cedula Correcta!.';
      if (cedula.length > 10) mensaje = 'RUC Correcto!.';
    }
    return mensaje
  }

  verifyCedula(cedula) {
    this.cedulaCorrecta = false;
    if (cedula.length == 10 || cedula.length == 13) {
      let cedulaCache: any[];
      let sumaPares = 0;
      let sumaImpares = 0;
      let sumaTotal: number;
      let verificador: number;
      let comparador = 10;
      cedulaCache = cedula.split('');
      for (let i = 0; i < 9; i = i + 2) {
        let impar = cedulaCache[i];
        impar = impar * 2;
        if (impar >= 10) {
          impar = impar - 9;
        }
        sumaImpares = sumaImpares + impar;
      }
      for (let i = 1; i < 8; i = i + 2) {
        const par = cedulaCache[i];
        sumaPares = sumaPares + parseInt(par, 0);
      }
      sumaTotal = sumaPares + sumaImpares;
      while (comparador < sumaTotal) {
        comparador = comparador + 10;
      }
      if (comparador - sumaTotal <= 9) {
        verificador = comparador - sumaTotal;
        if (verificador == cedulaCache[9]) {
          this.cedulaCorrecta = true;
        } else {
          this.cedulaCorrecta = false;

        }
      }
    }
  }

  objetoCliente() {
    this.cliente_guardar = new Cliente('', '', '', '', '', '', '', '', '', '', '', '', '');

  }
  mostrarrr() {
    this.desplegar = true;
    this.objetoCliente();
  }

  ngOnInit() {
    this.mostrarCliente();
  }
  registrar(form) {
    // if (confirm('Desea Guardar')) {
      this.cliente_guardar.fk_usuario = this.identity;
      this.today = new Date;

      this.cliente_guardar.cli_fecha = this.today.toString();
      this._clienteServicio.addCliente(this.cliente_guardar).subscribe(
        response => {
          if (response.cliente) {
            this.cliente_guardar = response.cliente;
            const cc = this.cliente_guardar.cli_cedula;
            const ap = this.cliente_guardar.cli_apellido;
            const nom = this.cliente_guardar.cli_nombre;
            this.alertRegister = 'El cliente ' + ap+ ' '+ nom + ' con C.C. '+ cc+ ' ' + '' +' , se ha registrado Correctamente!..';
            this.objetoCliente();
            this.mostrarCliente();
            form.reset();
            this.cedulaCorrecta = null;
            setTimeout(() => {
              this.alertRegister = null;
              this.limpiar();
            }, 3000);
          } else {
            // this.objetoCliente();
            console.log('no se ha guardado');
            
          }
          // alert('Se ha Guardado Corerctamente');
          // this.alertRegister = "se ha ingresado correctamente";
          // this.mostrarCliente();
        },
        error => {
          const errorMessage = error as any;
          if (errorMessage != null) {
            const body = JSON.parse(error._body);
            this.alertRegister = body.message;
          }
          this.objetoCliente();

          setTimeout(() => {
            this.alertRegister = null;
            this.limpiar();
          }, 3000);
        }
      )
    // }
    this.objetoCliente();

  }

  contador(){
    for (let i = 0; i < this.cliente_mostrar.length; i++) {
      const element = this.cliente_mostrar[i];
      element.index=i+1;
    }
  }

  mostrarCliente() {
    this._clienteServicio.getCliente(this.token).subscribe(
      response => {
        if (response.cliente) {
          this.cliente_mostrar = response.cliente;
          this.bufferCiente = response.cliente;
          this.contador();
        }
      },
      error => {
        console.log(<any>error);

      }
    )
  }
  delete(id) {
    if (confirm('Desea Eliminar')) {
      this._clienteServicio.deleteCliente(id).subscribe(
        response => {
          if (!response.una) {
            alert('Eliminado con Exito');
          }
          else {
            console.log('error al eliminar');
          }
          this.mostrarCliente();
        },
        error => {
          var errorMessage = <any>error;
          if (errorMessage != null) {
            var body = JSON.parse(error._body)
            this.alertRegister = body.message;
            console.log(error);
          }
        }
      )
    }
  }
  limpiar() {
    this.objetoCliente();
    this.numeroCorrecto = true;
    this.cedulaCorrecta = null;
  }
  public numeroCorrecto: boolean;
  public cedulasiguales: string;
  comprobarNumero(numero) {
    this.numeroCorrecto = false;
 
      this._statisticsService.cedulaCliente().subscribe(
        response => {
            response.cedulas.clientes.sort((a,b)=>(b.cli_cedula - a.cli_cedula));
            this.cedulasiguales=response.cedulas.clientes[0].cli_cedula;
            let conteo=0;
            //console.log(response.cedulas.clientes);
            if (numero != '') {
              for (const ced of response.cedulas.clientes) {
                const ce = ced.cli_cedula;
                if (numero == ce) {
                  conteo++;
                }
              }
              if (conteo == 0) {
                this.numeroCorrecto = true;
              } else if (conteo >= 1) {
                this.numeroCorrecto = false;
              }
         } 
        }
      )
  }

}
