import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { GLOBAL } from './global';
import 'rxjs/add/operator/map';
import { map } from 'rxjs/operators';
import { Producto } from '../models/producto';

@Injectable()

export class ProductoService {
  public url: string;

  constructor(private _http: Http) {
    this.url = GLOBAL.url;
  }


  addProducto(producto_registro: Producto) {
    let token = (localStorage.getItem('token'));
    let params = JSON.stringify(producto_registro)
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.post(this.url + 'guardarproducto', params, { headers: headers }).map(res => res.json());
  }

  getProducto(token) {
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'mostrar-productos', { headers: headers }).map(res => res.json());
  }

  mostrarUnProducto(id: string) {
    let token = (localStorage.getItem('token'));
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'producto/' + id, { headers: headers }).map(res => res.json());
  }


  editProducto(id: string, producto: Producto) {
    let token = (localStorage.getItem('token'));
    let params = JSON.stringify(producto);
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.put(this.url + 'actualizar-producto/' + id, params, { headers: headers }).map(res => res.json());
  }

  deleteProducto(id: string) {
    let token = (localStorage.getItem('token'));
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.delete(this.url + 'delete-producto/' + id, { headers: headers }).map(res => res.json());
  }
  actualizarPromedio(productoActualizar) {
    const params = JSON.stringify(productoActualizar);
    console.log('ingresando productos');
    let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.put(this.url + 'actualizar-promedio', params, { headers: headers }).map(res => res.json());
  
  }
}
