import { Component, OnInit } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { DetalleCompraService } from 'src/app/services/detalle-compra.service';
import { GLOBAL } from 'src/app/services/global';
import { detalleCompra } from 'src/app/models/detalleCompra';
import { Compra } from 'src/app/models/compra';
import { CompraService } from 'src/app/services/compra.service';
import { stringify } from 'querystring';
import { StatisticsService } from 'src/app/services/statistics.service';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Location } from '@angular/common';
import * as moment from 'moment';
import { buffer } from 'rxjs/operators';


@Component({
  selector: 'app-factura-compra',
  templateUrl: './factura-compra.component.html',
  styleUrls: ['./factura-compra.component.css'],
  providers: [StatisticsService],
})
export class FacturaCompraComponent implements OnInit {
  public identity;
  public token;
  public url: string;
  public mostrar_detalle: detalleCompra;
  public mostrar_compra: any;
  public statistics;
  constructor(
    private _usuarioService: UsuarioServiceService,
    private _serviceDetalleCompra: DetalleCompraService,
    private _serviceCompra: CompraService,
    private _statisticsService: StatisticsService,
    private _route: ActivatedRoute,
    private _location: Location,
    private _router: Router,
  ) {
    this.identity = this._usuarioService.getIdentity();
    this.token = this._usuarioService.getToken();
    this.url = GLOBAL.url;
  }


  regresar() {
    this._router.navigate(['/reporte']);
  }
  //imprime con estilos pero solo una vez
  printDiv(nombreDiv) {
    var contenido = document.getElementById(nombreDiv).innerHTML;
    var contenidoOriginal = document.body.innerHTML;
    document.body.innerHTML = contenido;
    window.print();
    document.body.innerHTML = contenidoOriginal;
    document.close();
  }
  //imprime sin stilos varias veces
  imprSelec(nombre) {
    var ficha = document.getElementById(nombre);
    var ventimp = window.open(' ', 'popimpr');
    ventimp.document.write(ficha.innerHTML);
    ventimp.document.close();
    ventimp.print();
    ventimp.close();
  }

  estadisticas() {
    this._statisticsService.getUserStatistics().subscribe(
      response => {
        this.statistics = response.conteo;
      }
    );
  }
  mostrarDetalleCompra() {
    this._serviceDetalleCompra.getdetallecompra().subscribe(
      response => {
        if (response.detallecompra) {
          this.mostrar_detalle = response.detallecompra;
          // console.log(this.mostrar_detalle);

        }
      },
      error => {

      }
    )
  }
  public bufferCompra: any = [] = [];
  public factura;
  media() {
    let media = 0;
    for (const compra of this.factura) {

    }
  }
  public buffer: any;
  // ************************************
  mostrarcompra() {
    this._serviceCompra.getCompra(this.token).subscribe(
      response => {
        if (response.compra) {
          this.mostrar_compra = response.compra;
          this.buffer = response.compra;
          //console.log('buffer', this.buffer);
          console.log(this.mostrar_compra);
          
          
          this.buscarFecha();
        }
      },
      error => {
      }
    )
  }
  // ***************************************************
  limpiar() {
    this.fechaInicio = null;
    this.fechaFin = null;
    this.fechaInicio = '';
    this.fechaFin = '';
    //console.log('limpiar cargado');
    this.mostrar_compra = this.buffer;
    this.sumatotal = '';
    this.numero = '';
    this.alertar = '';

  }
  onPrint() {
    window.print();
  }
  public compra: Compra;
  public totalsuma: number;
  gastoTotal() {
    this._statisticsService.getTotalgastoCompra().subscribe(
      response => {
        this.compra = response.compra;
        let suma = 0;
        for (const total of response.costos) {
          const costos = total.com_total;
          suma = suma + costos;
        }
        this.totalsuma = suma;
      },
      error => {

      }
    )
  }
  ngOnInit() {
    this.estadisticas();
    this.mostrarDetalleCompra();
    this.mostrarcompra();
    this.gastoTotal();
  }


  public fechaInicio;
  public fechaFin;
  public asd
  public variabletemporal = [];
  public sumatotal;
  public numero;
  public alertar;
  buscarFecha() {
    //console.log('inicio', this.fechaInicio, 'fin', this.fechaFin);

    if (this.fechaFin && this.fechaInicio) {
      let inicio = moment(this.fechaInicio);
      let fin = moment(this.fechaFin);

      if (inicio > fin) {
        alert('La fecha de inicio no puede ser anterior a la de fin');
      } else {
        this.asd = [];
        for (let i = 0; i < this.buffer.length; i++) {
          const element = this.buffer[i];
          const fecha = moment(element.com_fechaIngreso);
          const defffechaInicio = fecha.diff(inicio, 'days');
          const defffechafin = fin.diff(fecha, 'days');
         // console.log('Dif In', defffechaInicio, 'Df Fin', defffechafin, 'Fecha: ', element.com_fechaIngreso);

          if (defffechaInicio >= 0 && defffechafin >= 0) {
            // console.log('fecha', element.det_fecha, '.inicio: ', defffechaInicio, '. fin: ', defffechafin);
            this.asd.push(element);
            // suma=suma+this.asd.
            // console.log(this.asd);
          }
          // else{
          //  this.alertar='No hay Ventas en esa Fecha';
          //  setTimeout(() => {
          //   this.alertar = null;
          // }, 2500);
          // }
        }
        this.mostrar_compra = this.asd;
        // console.log('valor temporal', this.venta);

        let suma = 0
        let num = 0;
        for (let i = 0; i < this.mostrar_compra.length; i++) {
          const element = this.mostrar_compra[i];
          suma = suma + element.ven_total;
          num = num + 1;
        }
        // console.log(' la suma ess',suma);

        // comprobacion en caso de que exista algo en el array la alerta se borra
        if (this.mostrar_compra && this.mostrar_compra.length >= 1) {
          this.alertar = null;
          // si no hay nada en el array se muestra la alerta
        } else if (!this.mostrar_compra || this.mostrar_compra.length <= 0) {
          this.alertar = 'No se Encuentran Coincidencias';
        }

        this.sumatotal = suma;
        this.numero = num
        //console.log(this.numero);
        //console.log('al fin', this.mostrar_compra);
        for (let i = 0; i < this.mostrar_compra.length; i++) {
          const element = this.mostrar_compra[i];
          element.index = i + 1;

        }
      }
    }
  }

}
