import { Component, OnInit } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { UnidadService } from 'src/app/services/unidad.service';
import { Unidad_Medida } from 'src/app/models/unidad_medida';
import { GLOBAL } from 'src/app/services/global';

@Component({
  selector: 'app-editar-unidad',
  templateUrl: './editar-unidad.component.html',
  styleUrls: ['./editar-unidad.component.css'],
  providers: [UnidadService, UsuarioServiceService]
})
export class EditarUnidadComponent implements OnInit {
  public token;
  public identity;
  public alertRegister;
  public editar_unidad: Unidad_Medida;
  public url: string;
  public today = new Date;

  constructor(
    private _usuarioSercicio: UsuarioServiceService,
    private _router: Router,
    private _route: ActivatedRoute,
    private _editarServicio: UnidadService,
  ) {
    this.identity = this._usuarioSercicio.getIdentity();
    this.token = this._usuarioSercicio.getToken();
    this.getToday();
    this.url = GLOBAL.url;
  }

  getToday() {
    this.today.getDate();
  }

  ngOnInit() {
    this.getMostrar();
  }
  getMostrar() {
    this._route.params.forEach((params: Params) => {
      let id = params['id'];
      this._editarServicio.mostrarUnaUnidad(id).subscribe(
        response => {
          if (!response.editar_unidad) {

            this.editar_unidad = response.unidad;
          }
        },
        error => {
          var errorMessage = <any>error;
          if (errorMessage != null) {
            var body = JSON.parse(error._body)
            this.alertRegister = body.message;
            console.log(error);
          }
        }
      )
    })
  }
  actualizar() {
      this.editar_unidad.um_fechaActualizacion=this.today.toString();
      this._route.params.forEach((params: Params) => {
        let id = params['id'];
        this._editarServicio.updateUnidad(id, this.editar_unidad).subscribe(
          response => {
            if (!response.editar_unidad) {
              console.log('');
              console.log('Actualizado Correctamente');
              
              //alert(' Actualizado Correctamente');
            }else
            {
              // alert(' Error');

            }

          },
          error => {
            var errorMessage = <any>error;
            if (errorMessage != null) {
              var body = JSON.parse(error._body)
              this.alertRegister = body.message;
              console.log(error);
            }
          }
        )
        this._router.navigate(['/unidadMedida'])
      })
    }
  

  regresar() {

    this._router.navigate(['/unidadMedida'])

  }

}
