  public today = new Date;



  public asc:boolean;
  cambiarOrden(celda) {
    if (this.asc == true) this.asc = false; else
      if (this.asc == false) this.asc = true;
    this.reordenarArray(celda);
  }

  
  reordenarArray(celda){
    if(this.asc==true){
      if(celda=='codigo'){
        this.mostrar_marca.sort((menor, mayor) => (menor.marca_codigo.localeCompare(mayor.marca_codigo)));
      }else if(celda=='descripcion'){
        // this.mostrar_marca.sort((menor, mayor) => (parseInt(mayor.prod_stock) - parseInt(menor.prod_stock)));
        this.mostrar_marca.sort((menor, mayor) => (menor.marca_descripcion.localeCompare(mayor.marca_descripcion)));

      }else if(celda=='fecha'){
        this.mostrar_marca.sort((menor,mayor)=>{
          const a = new Date(menor.marca_fecha);
          const b = new Date(mayor.marca_fecha);
          if (a == b) return 0
          if (a > b) return -1
          if (a < b) return 1
        })
        
      }
    } else if(this.asc==false){
      if(celda=='codigo'){
        this.mostrar_marca.sort((menor, mayor) => (mayor.marca_codigo.localeCompare(menor.marca_codigo)));
      }else if(celda=='descripcion'){
        // this.mostrar_marca.sort((menor, mayor) => (parseInt(menor.prod_stock) - parseInt(mayor.prod_stock)));
        this.mostrar_marca.sort((menor, mayor) => (mayor.marca_descripcion.localeCompare(menor.marca_descripcion)));

      }else if(celda=='fecha'){
        this.mostrar_marca.sort((menor,mayor)=>{
          const a = new Date(menor.marca_fecha);
          const b = new Date(mayor.marca_fecha);
          if (a == b) return 0
          if (a > b) return 1
          if (a < b) return -1
        })
        
      }
    }
    for (let i = 0; i < this.mostrar_marca.length; i++) {
      const element = this.mostrar_marca[i];
      element.index=i+1;
    }
  }




