import { Component, OnInit } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { GLOBAL } from 'src/app/services/global';
import { Descuento } from 'src/app/models/descuento';
import { DescuentoService } from 'src/app/services/descuento.service';
import { Router } from '@angular/router';
import { Location } from '@angular/common';

@Component({
  selector: 'app-descuento',
  templateUrl: './descuento.component.html',
  styleUrls: ['./descuento.component.css']
})
export class DescuentoComponent implements OnInit {
  public identity;
  public token;
  public url: string;
  public descuento_guardar: Descuento;
  public descuento_mostrar: any;
  public alertRegister: any;
  public today= new Date();
  public verDescuento:Descuento;

  constructor(
    private _serviceUsuario: UsuarioServiceService,
    private _serviceDescuento: DescuentoService,
    private _router:Router,
    private _location:Location,
    // private 
  ) {
    this.identity = this._serviceUsuario.getIdentity();
    this.token = this._serviceUsuario.getToken();
    this.url = GLOBAL.url;
    this.objetoDescuento();
    this.mostrarDescuento();
    this.codigoAletorio();
  }
  

  public nombre;
  public cadenas;
  codigoAletorio() {
    let chars = '0123456789';
    let size = '5';
    let cadena = '';
    for (let i = 0; i < 5; i++) {
      cadena += chars[Math.floor(Math.random() * (chars.length))];
    }
    this.nombre = "DES"
    this.cadenas = cadena;
    this.descuento_guardar.des_codigo = this.nombre + this.cadenas;

  }

  objetoDescuento() {
    this.descuento_guardar = new Descuento('', '', '', 0, 0, 9999999, '','');
  }

  registrar(form) {
    this.today = new Date;
    this.descuento_guardar.fk_usuario=this.identity;
      this.descuento_guardar.des_fecha = this.today.toDateString();
      this._serviceDescuento.addDescuento(this.descuento_guardar).subscribe(
        response => {
          if (response.descuento) {

            this.descuento_guardar = response.descuento;
            
            // alert('Se ha Guardado Corerctamente');
            // this.mostrarDescuento();
            this.objetoDescuento();
            form.reset();
            setTimeout(() => {
              this.alertRegister = null;
            }, 3000);
          } else {
            alert('error al Guardar');
            console.log('no se puede registrar');
          }
          this.codigoAletorio();

        },
        error => {
          const errorMessage = error as any;
          if (errorMessage != null) {
            const body = JSON.parse(error._body);
            this.alertRegister = body.message;
          }
          this.objetoDescuento();
          this.codigoAletorio();

          setTimeout(() => {
            this.alertRegister = null;
            this.limpiar(form);
          }, 3000);
        }
      )
    
    this.objetoDescuento();
    this.mostrarDescuento();

  }

  limpiar(form){
    this.alertRegister = '';
    form.reset();
  }

  contador(){
    for (let i = 0; i < this.descuento_mostrar.length; i++) {
      const element = this.descuento_mostrar[i];
      element.index=i+1;
    }
  }

  mostrarDescuento() {
    this._serviceDescuento.getDescuento(this.token).subscribe(
      response => {
        if (response.descuento) {
          this.descuento_mostrar = response.descuento;
          this.contador();          
        }
      },
      error => {
        console.log(<any>error);

      }
    )
  }
  delete(id) {
    if (confirm('Desea Eliminar')) {
      this._serviceDescuento.deleteDescuento(id).subscribe(
        response => {
          if (response.descuento) {
            console.log('eliminado');
            //alert('Eliminado con Exito');
          }
          else {
            console.log('error al eliminar');
            //alert('Error al Eliminar');
          }
          this.mostrarDescuento();
        },
        error => {
          var errorMessage = <any>error;
          if (errorMessage != null) {
            var body = JSON.parse(error._body)
            this.alertRegister = body.message;
            console.log(error);
          }
        }
      )
    }
  }
  regresar() {
    this._router.navigate(['/menu']);
  }

  ngOnInit() {
    this.mostrarDescuento();
  }

}
