  public detalles;
  getTotales(arrayProd) {
    let totalIva = 0;
    let totalSubtotal = 0;
    let totalTotal = 0;
    for (const producto of arrayProd) {
      producto.total = 0;
      producto.subTotal = 0;
      producto.ivaValor = 0;
      producto.cantidadTotal = 0;
      producto.fechas = [];
      for (const detalle of this.detalles) {
        if (producto._id == detalle.fk_producto._id) {
          producto.fechas.push(detalle.det_fecha);
          producto.total += detalle.det_total;
          producto.ivaValor += detalle.det_ivaValor;
          producto.subTotal += detalle.det_subtotal;
          producto.cantidadTotal += detalle.det_unidades;
          totalTotal += detalle.det_total;
          totalIva += detalle.det_ivaValor;
          totalSubtotal += detalle.det_subtotal;
        }
      }
    }
    arrayProd.totalIva = totalIva;
    arrayProd.totalSubtotal = totalSubtotal;
    arrayProd.totalTotal = totalTotal;
    this.mostrar = arrayProd;
  }

