  public asc: boolean;
  cambiarOrden(celda) {
    if (this.asc == true) this.asc = false; else
      if (this.asc == false) this.asc = true;
    this.reordenarArray(celda);
  }

  reordenarArray(celda) {
    if (this.asc == true) {
      if (celda == 'codigo') {
        this.variabletemporal.sort((menor, mayor) => (menor.fk_venta.fk_cliente.cli_apellido.localeCompare(mayor.fk_venta.fk_cliente.cli_apellido)));
      } else if (celda == 'numero') {
        this.variabletemporal.sort((menor, mayor) => (parseInt(mayor.det_detalle) - parseInt(menor.det_detalle)));
      } else if (celda == 'fecha') {
        this.variabletemporal.sort((menor, mayor) => {
          const a = new Date(menor.det_fecha);
          const b = new Date(mayor.det_fecha);
          if (a == b) return 0
          if (a > b) return -1
          if (a < b) return 1
        })
      } else if (celda == 'alto') {
        this.variabletemporal.sort((menor, mayor) => ((mayor.fk_venta.ven_total) - (menor.fk_venta.ven_total)));

      }
    } else if (this.asc == false) {
      if (celda == 'codigo') {
        this.variabletemporal.sort((menor, mayor) => (mayor.fk_venta.fk_cliente.cli_apellido.localeCompare(menor.fk_venta.fk_cliente.cli_apellido)));
      } else if (celda == 'numero') {
        this.variabletemporal.sort((menor, mayor) => (parseInt(menor.det_detalle) - parseInt(mayor.det_detalle)));
      } else if (celda == 'fecha') {
        this.variabletemporal.sort((menor, mayor) => {
          const a = new Date(menor.det_fecha);
          const b = new Date(mayor.det_fecha);
          if (a == b) return 0
          if (a > b) return 1
          if (a < b) return -1
        })

      } else if (celda == 'alto') {
        this.variabletemporal.sort((menor, mayor) => ((menor.fk_venta.ven_total) - (mayor.fk_venta.ven_total)));
      }
    }
  }




