import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { GLOBAL } from './global';
import { Iva } from '../models/iva';

@Injectable({
  providedIn: 'root'
})
export class IvaService {
  public url: string;
  constructor(
    private _http: Http
  ) {
    this.url = GLOBAL.url;
  }

  addIva(iva_guardar: Iva) {
    let token = (localStorage.getItem('token'));
    let params = JSON.stringify(iva_guardar)
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.post(this.url + 'guardar-iva', params, { headers: headers }).map(res => res.json());

  }
  //mostrar todos los datos
  getIva(token) {
    //let token=localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'mostrar-iva', { headers: headers }).map(res => res.json());
  }
  //eliminar marca
  deleteIva(id: string) {
    let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.delete(this.url + 'delete-iva/' + id, { headers: headers }).map(res => res.json());;
  }
  //mostrar un producto

  mostrarUnIva(id: string) {
    let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'get-iva/' + id, { headers: headers }).map(res => res.json());
  }

  editIva(id: string, iva: Iva) {
    let token = localStorage.getItem('token');
    let params = JSON.stringify(iva);
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.put(this.url + 'update-iva/' + id, params, { headers: headers }).map(res => res.json());
  }

}

