import { Component, OnInit } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { DetalleVentaService } from 'src/app/services/detalle-venta.service';
import { Params, ActivatedRoute } from '@angular/router';
import { detalleVenta } from 'src/app/models/detalleVenta';

@Component({
  selector: 'app-detalle-ventas',
  templateUrl: './detalle-ventas.component.html',
  styleUrls: ['./detalle-ventas.component.css'],
  providers:[UsuarioServiceService,DetalleVentaService],
})
export class DetalleVentasComponent implements OnInit {
  public identity;
  public token;
  constructor(
    private _usuarioService: UsuarioServiceService,
    private _detalleVenta:DetalleVentaService,
    private _route:ActivatedRoute,
  ) {
    this.identity = this._usuarioService.getIdentity();
    this.token = this._usuarioService.getToken();
    this.getmostrar();

  }
  
  getmostrar() {
  this._route.params.forEach((params: Params) => {
    if (params['id'] != undefined) {
      let id = params['id'];
      this.loadDetalle(id);
    }
  })
}
onPrint(){
  window.print();
}   
public mostrardetalle:detalleVenta;

loadDetalle(id) {
  this._detalleVenta.mostrarUnDetalle(id).subscribe(
    response => {
      if (response.venta) {
        this.mostrardetalle = response.venta;
       // console.log('respuesta del servidor',response);
      }
      else{
        console.log('no se puede mostrar');
      }
    },
    error => {
      var errorMessage = <any>error;
      if (errorMessage != null) {
        var body = JSON.parse(error._body)
        console.log(error);
      }
    }
  )
}
  ngOnInit() {
    this.getmostrar();
  }

}
