import { Component, OnInit } from '@angular/core';
import { Iva } from 'src/app/models/iva';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { GLOBAL } from 'src/app/services/global';
import { IvaService } from 'src/app/services/iva.service';

@Component({
  selector: 'app-editar-iva',
  templateUrl: './editar-iva.component.html',
  styleUrls: ['./editar-iva.component.css']
})
export class EditarIvaComponent implements OnInit {
  public token;
  public identity;
  public url: string;
  public editar_iva: Iva;
  public alertRegister;
  public today = new Date;

  constructor(
    private _ivaServicio: IvaService,
    private _router: Router,
    private _usuarioServicio: UsuarioServiceService,
    private _route: ActivatedRoute,
  ) {
    this.identity = this._usuarioServicio.getIdentity();
    this.token = this._usuarioServicio.getToken();
    this.url = GLOBAL.url;
    this.getToday();
    this.getIva();

  }
  getToday() {
    this.today.getDate();
  }

  getIva() {
    this._route.params.forEach((params: Params) => {
      let id = params['id'];
      this._ivaServicio.mostrarUnIva(id).subscribe(
        response => {
          if (!response.editar_iva) {
            this.editar_iva = response.iva;
          }

        },
        error => {
          var errorMessage = <any>error;
          if (errorMessage != null) {
            var body = JSON.parse(error._body)
            this.alertRegister = body.message;
            console.log(error);
          }
        }
      )
    })
    // this.getIva();

  }
  regresar() {
    this._router.navigate(['iva']);
  }
  ngOnInit() {
    this.getIva();
  }

  actualizar() {
    // if (confirm('Desea Guardar')) {
      this.editar_iva.iva_fechaActualizacion=this.today.toString();
      this._route.params.forEach((params: Params) => {
        let id = params['id'];
        this._ivaServicio.editIva(id, this.editar_iva).subscribe(
          response => {
            if (response.iva) {
              // console.log('exito');
              // alert('Actualizado Correctamente');
              this.alertRegister = "Actualizado Correctamente";
              this.getIva();
            }
            else {
              console.log('Error Al actualizar');
            }
            
          },
          error => {
            var errorMessage = <any>error;
            if (errorMessage != null) {
              var body = JSON.parse(error._body)
              this.alertRegister = body.message;
              console.log(error);
            }
          }
          )
          // this._router.navigate(['/iva']);
      })
    // }
  }

}
