import { Component, OnInit } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { Iva } from 'src/app/models/iva';
import { IvaService } from 'src/app/services/iva.service';
import { Router } from '@angular/router';
import { GLOBAL } from 'src/app/services/global';
import { Location } from '@angular/common';

@Component({
  selector: 'app-iva',
  templateUrl: './iva.component.html',
  styleUrls: ['./iva.component.css']
})
export class IvaComponent implements OnInit {
  public token;
  public identity;
  public url: string;
  public alertRegister;
  public iva_guardar: Iva;
  public iva_mostrar;
  public today = new Date;


  constructor(
    private _usuarioServicio: UsuarioServiceService,
    private _ivaservicio: IvaService,
    private _router: Router,
    private _location:Location,
  ) {
    this.identity = this._usuarioServicio.getIdentity();
    this.token = this._usuarioServicio.getToken();
    this.url = GLOBAL.url;
    this.objetoIva();
    this.getToday();
    this.getIva();

  }

//   public nombre;
// public cadenas;
//   codigoAletorio(){
//     let chars='0123456789';
//     let size='5';
//     let cadena='';
//     for( let i=0;i<5;i++){
//       cadena+=chars[Math.floor(Math.random()*(chars.length))];
//     }
//     this.nombre="MA"
//     this.cadenas=cadena;
//     this.marca_guardar.marca_codigo=this.nombre+this.cadenas;
    
//   }

  getToday() {
    this.today.getDate();
  }

  regresar() {
    this._location.back();
  }
  objetoIva() {
    this.iva_guardar = new Iva('', 0, '', '','','');

  }

  registrar(form) {

    this.iva_guardar.fk_usuario=this.identity;
    // this.iva_guardar.iva_fecha =   this.today.toLocaleDateString() + "  "+ this.today.getHours()+ ":" + this.today.getMinutes() + ":"+this.today.getSeconds();
    this.today = new Date;
      this.iva_guardar.iva_fecha = this.today.toString();
      this._ivaservicio.addIva(this.iva_guardar).subscribe(
        response => {
          if (response.iva) {
            this.iva_guardar = response.iva;
            // console.log('nose ha guaradao buscar el error');
            const Descripcion = this.iva_guardar.iva_valor;
            this.alertRegister = 'El IVA: ' + Descripcion + ', se ha registrado Correctamente!..';
            this.objetoIva();
            form.reset();
            setTimeout(() => {
              this.alertRegister = null;
            }, 3000);
          }
          else {
            this.objetoIva();
            console.log('Error al Registrar');
            this.getIva();
          }
        },
        error => {
          const errorMessage = error as any;
          if (errorMessage != null) {
            const body = JSON.parse(error._body);
            this.alertRegister = body.message;
          }
          this.objetoIva();
          setTimeout(() => {
            this.alertRegister = null;
            this.limpiar(form);
          }, 3000);
        }
      )
    
    this.objetoIva();
    this.getIva();
  }
  public ivaActual: number;
  public iva: any;
  limpiar(form){
    this.alertRegister = '';
    form.reset();
  }

  contador(){
    for (let i = 0; i < this.iva_mostrar.length; i++) {
      const element = this.iva_mostrar[i];
      element.index=i+1;
    }
  }

  getIva() {
    this._ivaservicio.getIva(this.token).subscribe(
      response => {
        if (response.iva) {
          this.iva_mostrar = response.iva;
          this.contador();          
        }
        else {
          console.log('error no se encuentran los datos');
        }
      },
      error => {
        console.log(<any>error);

      }
    )
  }
  delete(id) {
    this._ivaservicio.deleteIva(id).subscribe(
      response => {
        if (!response.una) {
          console.log('eliminado');

        }
        else {
          console.log('error al eliminar');
          alert('error al eliminar');
        }
        this.getIva();
      },
      error => {
        var errorMessage = <any>error;
        if (errorMessage != null) {
          var body = JSON.parse(error._body)
          this.alertRegister = body.message;
          console.log(error);
        }
      }
    )

  }
  ngOnInit() {
    this.getIva();
  }


}
