import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { GLOBAL } from './global';
import { Marca } from '../models/marca';
import { map } from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class MarcaService {
  public url: string;
  constructor(private _http: Http) {
    this.url = GLOBAL.url;
  }

  addMarca(token, marca_guardar: Marca) {
    //let token=(localStorage.getItem('token'));
    let params = JSON.stringify(marca_guardar)
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.post(this.url + 'guardar-marca', params, { headers: headers }).map(res => res.json());
  }
  //mostrar todos los productos
  getMarca(token) {
    //let token=localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'mostrar-marca', { headers: headers }).map(res => res.json());
  }
  //eliminar marca
  deleteMarca(id: string) {
    let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.delete(this.url + 'delete-marca/' + id, { headers: headers }).map(res => res.json());;
  }
  //mostrar un producto

  mostrarUnaMarca(_id: string) {
    let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url+'mostrar-una-marca/'+_id,{headers:headers}).map(res=>res.json());
  }

  editMarca(_id:string,marca:Marca){
    let token=localStorage.getItem('token');
    let params=JSON.stringify(marca);
    let headers=new Headers({'Content-Type':'application/json','Authorization':token});
    return this._http.put(this.url+'update-marca/'+_id,params,{headers:headers}).map(res=>res.json());
  }
}
