import { Component, OnInit, DoCheck } from '@angular/core';
import { Usuario } from './models/usuario';
import { UsuarioServiceService } from './services/usuario-service.service';
// import { Body } from '@angular/http/src/body';
import { GLOBAL } from 'src/app/services/global';
import { ActivatedRoute, Router } from '@angular/router';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  providers: [
    UsuarioServiceService
  ]
  // styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit, DoCheck {
  public title = 'Sistema de Control de Ventas';
  // public usuario = Usuario; //nuevo objeto
  // todo los objetos del usuario que este identificado dentro del login
  // darle el valor del objeto, si esta relleno va a estar logueado
  public identity;
  public usuario: Usuario;
  // tslint:disable-next-line: variable-name
  public usuario_registro: Usuario;
  public token;
  public url: string;
  public titulousuario;
  public titulo;
  public today: Date;

  // asignar un valor a una propiedad de una clase
  // darle un valor al objeto usuario
  constructor(
    // tslint:disable-next-line: variable-name
    private _usuarioServicio: UsuarioServiceService,
    // tslint:disable-next-line: variable-name
    private _router: Router
  ) {
    // this.titulo = ' Papeleria San Francisco';
    this.usuario = new Usuario('', '', '', '', '', '', '', '','', '', '', 'ROL_USER', '', '');
    this.identity = this._usuarioServicio.getIdentity();
    this.token = this._usuarioServicio.getToken();
    // console.log(this.identity);
    this.url = GLOBAL.url;
    //  this.usuario_registro = new Usuario('','','','','','','','ROLE_USER','');
  }

  ngOnInit() {
    // asignar un valor
    // console.log(this.identity);
    // console.log(this.token);

  }
  inventario(){
    if (this.identity.usu_rol=='ROL_ADMIN') {
      this._router.navigate(['/reporte']);
    }else{
      if(this.identity.usu_rol=='ROL_USUARIO'){
        this._router.navigate(['/saldos']);

      }
    }
  }
  ngDoCheck() {
    this.identity = this._usuarioServicio.getIdentity();
    this.token = this._usuarioServicio.getToken();
    this.today = new Date();
  }
  // logeuear al usuario

  lougout() {
    localStorage.removeItem('identity');
    localStorage.removeItem('token');
    localStorage.clear();
    this.identity = null;
    this.token = null;
    this.identity = this._usuarioServicio.getIdentity();
    this._router.navigate(['/login']);
  }

  actualizarUsuario() {
    this._router.navigate(['/actualizar_usuario']);

  }
}
