import { Component, OnInit, DoCheck, OnChanges } from '@angular/core';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { GLOBAL } from 'src/app/services/global';
import { Categoria } from 'src/app/models/categoria';
import { CategoriaService } from 'src/app/services/categoria.service';
import { Location } from '@angular/common';


@Component({
  selector: 'app-actualizar-categoria',
  templateUrl: './actualizar-categoria.component.html',
  styleUrls: ['./actualizar-categoria.component.css'],
  providers: [UsuarioServiceService, CategoriaService]

})
export class ActualizarCategoriaComponent implements OnInit, OnChanges {

  public titulo;
  //public categoria_registrar:Categoria;
  public identity;
  public url: string;
  public alertRegister;
  public numbers = new Array(10);
  public editar_caegoria: Categoria;
  public status;
  public today = new Date;
  public token;

  constructor(
    private _usuarioservicio: UsuarioServiceService,
    private _router: Router,
    private _editar_categoria: CategoriaService,
    private _route: ActivatedRoute,
    private _location:Location,
  ) {
    this.identity = this._usuarioservicio.getIdentity();
    this.token = this._usuarioservicio.getToken();
    this.url = GLOBAL.url;
    //this.editar_caegoria =new Categoria('','','','');
    this.getToday();
    this.titulo = "Guardar"
  }

  getToday() {
    this.today.getDate();
  }
  getMosstrarUnaCategoria() {
    this._route.params.forEach((params: Params) => {
      let id = params['id'];
      this._editar_categoria.mostrarUnaCategoria(id).subscribe(
        response => {

          if (!response.editar_caegoria) {
            this.editar_caegoria = response.categoria;
          }
          else {
            console.log('error en el servidor');
          }
        },
        error => {
          var errorMessage = <any>error;
          if (errorMessage != null) {
            var body = JSON.parse(error._body)
            this.alertRegister = body.message;
          }
        }
      )
    })
  }

  actualizarCategoria() {
    // if (confirm('Desea Guardar')) {
      this.editar_caegoria.cat_modificar=this.identity;
      this.today = new Date;
      this.editar_caegoria.cat_fechaActualizacion=this.today.toString();
      this._route.params.forEach((params: Params) => {
        let id = params['id'];
        this._editar_categoria.editCategoria(id, this.editar_caegoria).subscribe(
          response => {
            if (!response.editar_caegoria) {
              //this._router.navigate(['/']);
              //this.editar_caegoria=response.categoria;
            } else {
            }
            this.alertRegister = "Actualizado Correctamente";
            // alert('La Categoria se ha actualizado Correctamente"');

          },
          error => {
            var errorMessage = <any>error;
            if (errorMessage != null) {
              var body = JSON.parse(error._body)
              this.alertRegister = body.message;
            }
            // this.editar_caegoria =new Categoria('','','','');
          }
        )
        this._router.navigate(['categoria/crearCategoria'])
      });
    // }
  }
  regresar() {
    // this._router.navigate(['/crearCategoria'])
    this._location.back();

  }

  ngOnInit() {
      this.getMosstrarUnaCategoria();
  }


  ngOnChanges() {

  }

}



