import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { GLOBAL } from './global';
import { Unidad_Medida } from '../models/unidad_medida';
import 'rxjs/add/operator/map';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class UnidadService {
  public url: string;
  constructor(
    private _http: Http
  ) {
    this.url = GLOBAL.url;
  }
  addUnidad(unidad_guardar: Unidad_Medida) {
    let token = (localStorage.getItem('token'));
    let params = JSON.stringify(unidad_guardar)
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.post(this.url + 'guardarUM', params, { headers: headers }).map(res => res.json());
  }

  getUnidad(token) {
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'mostrarUM', { headers: headers }).map(res => res.json());

  }

  mostrarUnaUnidad(id) {
    let token = (localStorage.getItem('token'));
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'mostrarUnidad/' + id, { headers: headers }).map(res => res.json());
  }

  updateUnidad(id: string, unidad: Unidad_Medida) {
    let token = (localStorage.getItem('token'));
    let params = JSON.stringify(unidad);
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.put(this.url + 'updateUM/' + id, params, { headers: headers }).map(res => res.json());
  }
  deleteUnidad(id: string) {
    let token = (localStorage.getItem('token'));
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.delete(this.url + 'deleteUM/' + id, { headers: headers }).map(res => res.json());

  }
}
