import { Injectable } from '@angular/core';
import { Http,Headers } from '@angular/http';
import { GLOBAL } from './global';
import { numeroFactura } from '../models/numero-venta';

@Injectable({
  providedIn: 'root'
})
export class NumeroVentaService {
  public url:string;
  constructor( private _http:Http) { 
    this.url=GLOBAL.url;
  }
  addNumero(token,guardar:numeroFactura){
    // let token=(localStorage.getItem('token'));
    let params = JSON.stringify(guardar)
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.post(this.url + 'registrar', params, { headers: headers }).map(res => res.json());
  }
    getNumero(){
      let token=localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'mostrar', { headers: headers }).map(res => res.json());
    }

    mostrarUnNumero(_id:string){
      let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url+'mostrar/'+_id,{headers:headers}).map(res=>res.json());
    }

    editarNumero(_id:string,factura:numeroFactura){
      let token=localStorage.getItem('token');
      let params=JSON.stringify(factura);
      let headers=new Headers({'Content-Type':'application/json','Authorization':token});
      return this._http.put(this.url+'actualizar/'+_id,params,{headers:headers}).map(res=>res.json());
    }

}
