import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { GLOBAL } from './global';
import { Compra } from '../models/compra';

@Injectable({
  providedIn: 'root'
})
export class CompraService {
  public url:string;
  constructor(
    private _http:Http,
  ) { 
    this.url=GLOBAL.url;
  }

  addCompra(compra:Compra){
    let token = (localStorage.getItem('token'));
    let params = JSON.stringify(compra)
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.post(this.url + 'guardar-compra', params, { headers: headers }).map(res => res.json());

  }

  getCompra(token){
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'mostrar-compra', { headers: headers }).map(res => res.json());
  }

  deleteCompra(id:string){
    let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.delete(this.url + 'delete-compra/' + id, { headers: headers }).map(res => res.json());;
  }

}
