import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { GLOBAL } from './global';
import { Configuracion } from '../models/configuracion';

@Injectable({
  providedIn: 'root'
})
export class ConfiguracionService {
  public url:string;
  constructor( 
    private _http:Http
  ) { 
    this.url=GLOBAL.url;
  }

  addconfiguracion(token,guardar_configuracion:Configuracion){
    // let token=localStorage.getItem('token');
    let params = JSON.stringify(guardar_configuracion)
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.post(this.url + 'guardar-configuracion', params, { headers: headers }).map(res => res.json());
  }

  get(){
    let token=localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'mostrar-configuracion', { headers: headers }).map(res => res.json());
  }
 
  mostrarUna(_id:string){
    let token=localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'mostrar-configuracion/'+_id, { headers: headers }).map(res => res.json());
  }

  editar(_id:string,marca:Configuracion){
    let token=localStorage.getItem('token');
    let params=JSON.stringify(marca);
    let headers=new Headers({'Content-Type':'application/json','Authorization':token});
    return this._http.put(this.url +'editar-configuracion/'+_id,params,{headers:headers}).map(res=>res.json());
  }

}
