import { Injectable } from '@angular/core';
import { GLOBAL } from './global';
import { Headers, Http } from '@angular/http';
import 'rxjs/add/operator/map';
import {map} from 'rxjs/operators'; 

@Injectable({
  providedIn: 'root'
})
export class DetalleVentaService {
  public url: string;
  constructor(
    private _http: Http
  ) {
    this.url = GLOBAL.url;
  }

  guardarDetalleVenta(detalleVenta) {
    const token = localStorage.getItem('token');
    const params = JSON.stringify(detalleVenta);
    const headers = new Headers({
      'Content-Type':'application/json', 
      'Authorization':token
    });
    return this._http.post(this.url + 'guardar-detalle-venta/', params, {headers: headers}).pipe(map(res => res.json()))
  }
  getdetalleventa(){
    const token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'mostrar-detalle-venta', { headers: headers }).map(res => res.json());
  }

  mostrarUnDetalle(_id: string) {
    let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url+'get-detalleventa/'+_id,{headers:headers}).map(res=>res.json());
  }
  prueba(id: string) {
    let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url+'get-detalleventa/'+id,{headers:headers}).map(res=>res.json());
  }
}


