import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { GLOBAL } from './global';
import { Venta } from '../models/venta';

@Injectable({
  providedIn: 'root'
})
export class VentaService {
  public url:string;
  constructor(
    private _http:Http,
  ) { 
    this.url=GLOBAL.url;
  }

  addVenta(venta:Venta){
    let token = (localStorage.getItem('token'));
    let params = JSON.stringify(venta)
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.post(this.url + 'guardar-venta', params, { headers: headers }).map(res => res.json());

  }

  getVenta(token){
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'mostrar-venta', { headers: headers }).map(res => res.json());
  }
  getNumeros(token){
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'numeros-venta', { headers: headers }).map(res => res.json());
  }

  deleteVenta(id:string){
    let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.delete(this.url + 'delete-venta/' + id, { headers: headers }).map(res => res.json());;
  }

}
