import { Component, OnInit } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { NumeroVentaService } from 'src/app/services/numero-venta.service';
import { Location } from '@angular/common';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { GLOBAL } from 'src/app/services/global';
import { numeroFactura } from 'src/app/models/numero-venta';

@Component({
  selector: 'app-editar-numero-venta',
  templateUrl: './editar-numero-venta.component.html',
  styleUrls: ['./editar-numero-venta.component.css']
})
export class EditarNumeroVentaComponent implements OnInit {
  public token;
  public identity;
  public url:string;
  constructor(
    private _usuarioService:UsuarioServiceService,
    private _numeroVentaService:NumeroVentaService,
    private _location:Location,
    private _router:Router,
    private _route:ActivatedRoute,
  ) { 
    this.identity=this._usuarioService.getIdentity();
    this.token=this._usuarioService.getToken();
    this.url=GLOBAL.url;  
    this.getmostrar();
  }
regresar(){
  this._router.navigate(['/numero-venta'])
}
  ngOnInit() {

  }
public numeros:numeroFactura;
  getmostrar(){
    this._route.params.forEach((params:Params)=>{
      let id=params['id'];
      this._numeroVentaService.mostrarUnNumero(id).subscribe(
        response=>{
          if( response.numero){
            this.numeros=response.numero;
            console.log(this.numeros);
            
          }
        },
        error=>{

        }
      )
    })
  }
  public alertRegister;
  actualizar(){
    this._route.params.forEach((params:Params)=>{
      let id=params['id'];
      this._numeroVentaService.editarNumero(id,this.numeros).subscribe(
        response=>{
          if(response.numero){
            this.alertRegister="Actualizado Correctamente"
          }else
          {
            console.log('error al actualizar');
          }
        }
      )
    })
  }
}
