import { Component, OnInit } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { GLOBAL } from 'src/app/services/global';
import { Params, ActivatedRoute, Router } from '@angular/router';
import { DescuentoService } from 'src/app/services/descuento.service';
import { Descuento } from 'src/app/models/descuento';

@Component({
  selector: 'app-editar-descuento',
  templateUrl: './editar-descuento.component.html',
  styleUrls: ['./editar-descuento.component.css']
})
export class EditarDescuentoComponent implements OnInit {
  public identity;
  public token;
  public url: string;
  constructor(
    private _usuarioServicio: UsuarioServiceService,
    private _route: ActivatedRoute,
    private _descuentoservice: DescuentoService,
    private _router: Router,
  ) {
    this.token = this._usuarioServicio.getToken();
    this.identity = this._usuarioServicio.getIdentity();
    this.url = GLOBAL.url;
  }
  public descuento_mostrar: Descuento;
  public alertRegister;
  getmostrar() {
    this._route.params.forEach((params: Params) => {
      let id = params['id'];
      this._descuentoservice.mostrarUnDescuento(id).subscribe(
        response => {
          if (response.descuento) {
            this.descuento_mostrar = response.descuento;
           // console.log(this.descuento_mostrar);

          }
        },
        error => {
          var errorMessage = <any>error;
          if (errorMessage != null) {
            var body = JSON.parse(error._body)
            this.alertRegister = body.message;
            console.log(error);
          }
        }
      )
    })
  }
  public editar_descuento;
  actualizar() {
    // if (confirm('Desea Guardar')) {
      this._route.params.forEach((params: Params) => {
        let id = params['id'];
        this._descuentoservice.editarDescuento(id, this.descuento_mostrar).subscribe(
          response => {
            if (response.descuento) {
              const descripcion=this.descuento_mostrar
              // this.editar_descuento=this.editar_descuento.response;
              this.alertRegister = "Actualizado Correctamente"
              // setTimeout(() => {
              //   this._router.navigate(['descuento'])
              // }, 3000);
            }
            else{
              console.log('error');
              
            }
          }
        )
        // this._router.navigate(['descuento'])
      })
    // }
  }

  regresar() {
    this._router.navigate(['descuento'])
  }
  ngOnInit() {
    this.getmostrar();
  }

}
