import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { GLOBAL } from './global';
import { Proveedor } from '../models/proveedor';

@Injectable({
  providedIn: 'root'
})
export class ProveedorService {
  public url: string;
  constructor(
    private _http: Http
  ) {
    this.url = GLOBAL.url;
  }

  addProveedor(proveedor: Proveedor) {
    let token = (localStorage.getItem('token'));
    let params = JSON.stringify(proveedor)
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.post(this.url + 'guardar-proveedor', params, { headers: headers }).map(res => res.json());


  }

  getProveedor(token) {
    //let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'mostrar-proveedor', { headers: headers }).map(res => res.json());

  }

  deleteProveedor(id: string) {
    let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.delete(this.url + 'delete-proveedor/' + id, { headers: headers }).map(res => res.json());;
  }
  mostrarUnProveedor(id: string) {
    let token = localStorage.getItem('token');
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.get(this.url + 'get-proveedor/' + id, { headers: headers }).map(res => res.json());


  }
  editarProveedor(id: string, proveedor: Proveedor) {
    let token = localStorage.getItem('token');
    let params = JSON.stringify(proveedor);
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.put(this.url + 'update-proveedor/' + id, params, { headers: headers }).map(res => res.json());
  }

}
