import { Injectable } from '@angular/core';
import { GLOBAL } from './global';
import { Http, Headers } from '@angular/http';
import { Kardex } from '../models/kardex';


@Injectable({
    providedIn: 'root'
})

export class KardexService {
    public url: string;
    constructor (
        private _http: Http
    ) {
        this.url = GLOBAL.url;
    }
    
    addKardex(token, kardex: Kardex) {
        let params = JSON.stringify(kardex);
        let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
        return this._http.post(this.url + 'guardar-kardex', params, {headers:headers}).map(res=>res.json());
    }
    getKardex(token) { 
        let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
        return this._http.get(this.url + 'mostrarkardex', {headers: headers}).map(res=>res.json());
    }
    getKardexByProduct(token, id) { 
        let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
        return this._http.get(this.url + 'mostrarkardex/'+id, {headers: headers}).map(res=>res.json());
    }
}