import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { GLOBAL } from './global';
import { Descuento } from '../models/descuento';

@Injectable({
  providedIn: 'root'
})
export class DescuentoService {
  public url:string;
  constructor(
    private _http:Http,
  ) { 
    this.url=GLOBAL.url;
  }
  addDescuento(descuento:Descuento){
    let token = (localStorage.getItem('token'));
    let params = JSON.stringify(descuento)
    let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
    return this._http.post(this.url + 'guardar-descuento', params, { headers: headers }).map(res => res.json());

  }

    getDescuento(token){
      let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
      return this._http.get(this.url + 'mostrar-descuento', { headers: headers }).map(res => res.json());
  
    }

    deleteDescuento(id:string){
      let token = localStorage.getItem('token');
      let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
      return this._http.delete(this.url + 'delete-descuento/' + id, { headers: headers }).map(res => res.json());;
  
    }


    mostrarUnDescuento(_id: string) {
      let token = localStorage.getItem('token');
      let headers = new Headers({ 'Content-Type': 'application/json', 'Authorization': token });
      return this._http.get(this.url+'mostrar-descuento/'+_id,{headers:headers}).map(res=>res.json());
    }
  
    editarDescuento(_id:string,descuento:Descuento){
      let token=localStorage.getItem('token');
      let params=JSON.stringify(descuento);
      let headers=new Headers({'Content-Type':'application/json','Authorization':token});
      return this._http.put(this.url+'update-descuento/'+_id,params,{headers:headers}).map(res=>res.json());
    }
}
