import { Component, OnInit } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { ConfiguracionService } from 'src/app/services/configuracion.service';
import { Location } from '@angular/common';
import { GLOBAL } from 'src/app/services/global';
import { Configuracion } from 'src/app/models/configuracion';

@Component({
  selector: 'app-editar-configuracion',
  templateUrl: './editar-configuracion.component.html',
  styleUrls: ['./editar-configuracion.component.css']
})
export class EditarConfiguracionComponent implements OnInit {
  public token;
  public identity;
  public url:string;
  constructor(
    private _usuarioService:UsuarioServiceService,
    private _router:Router,
    private _configuracionService:ConfiguracionService,
    private _locaion:Location,
    private _route:ActivatedRoute,
  ) {
    this.token=this._usuarioService.getToken();
    this.identity=this._usuarioService.getIdentity();
    this.url=GLOBAL.url;
    this.getmostrar();
   }
   regresar(){
     this._router.navigate(['/configuracion']);
   }

  ngOnInit() {
  }
  public configuracion: Configuracion;
  getmostrar(){
    this._route.params.forEach((params:Params)=>{
      let id = params['id'];
      this._configuracionService.mostrarUna(id).subscribe(
        response=>{
          if(response.conf){
            this.configuracion=response.conf;
            //console.log(this.configuracion);
            
          }
        },
        error=>{

        }
      )
    })
  }
  public alertRegister;
  actualizar(){
    this._route.params.forEach((params:Params)=>{
      let id=params['id'];
      this._configuracionService.editar(id,this.configuracion).subscribe(
        response=>{
          if(response.conf){
            this.alertRegister="Actualizado Correctamente"
          }else{
            console.log('error al actualizar');
            
          }
        },
        error=>{

        }
      )
    })
  }

}
