import { Component, OnInit, Output, EventEmitter, DoCheck } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { ProductoService } from 'src/app/services/producto.service';
import { Producto } from 'src/app/models/producto';
import { GLOBAL } from 'src/app/services/global';
import { Router } from '@angular/router';

import * as jsPDF from 'jspdf';
import { StatisticsService } from 'src/app/services/statistics.service';


@Component({
  selector: 'app-listar-producto',
  templateUrl: './listar-producto.component.html',
  styleUrls: ['./listar-producto.component.css'],
  providers: [UsuarioServiceService, ProductoService,StatisticsService],
})
export class ListarProductoComponent implements OnInit,DoCheck {
  public identity;
  public token;
  public url: string;
  public producto_mostrar: any[] = [];
  public verProducto: Producto;

  constructor(
    private _usuarioServicio: UsuarioServiceService,
    private _productoServicio: ProductoService,
    private _router: Router,
    private _statistic:StatisticsService,
  ) {
    this.asc=true;
    this.identity = this._usuarioServicio.getIdentity();
    this.token = this._usuarioServicio.getToken();
    this.url = GLOBAL.url;
    this.estadisticas();
  }


  

  public today = new Date;

  ngDoCheck() {
    this.today = new Date();  
  }
  
  public countFindeds: number;
  public bufferProducto: any[] = [];

  public statistics;
  estadisticas(){
    this._statistic.getUserStatistics().subscribe(
      response => {
        if (response.conteo) {
          this.statistics = response.conteo;
        }
      }
    );
  }
  detalle() {
    var id = document.getElementById("detalleproducto");

    // var imgData='bjdjt-puacn.webp'
    var pdf = new jsPDF({
      orientacion: '3',
      unit: 'pt',
      format: 'A4',
      posicicion: 3,
    });
    pdf.setFont('times')    //tipo de letra  
    pdf.setFontSize(22);      //tama de letra

    // pdf.addImage(imgData,'JPEG', 15, 40, 18, 16)

    pdf.setTextColor(84, 201, 47)   //color RGB
    pdf.text("Papeleria & Bazar - 'San Francisco' ", 150, 50); //posicion de la letra columna fila

    pdf.setLineWidth(3) //grueso de la linea
    pdf.setDrawColor(251, 152, 4)   // color d ela linea
    pdf.line(80, 67, 525, 67) //  posicion de la linea columna, fila, columna, fila

    // pdf.setTableHeaderRow([1500, 7000]);
    // pdf.table(120, 120, 120, [],120)
    // pdf.table(100, 100,100,[],100)

    pdf.setLineWidth(0.3)
    pdf.setDrawColor(150, 132, 104)
    pdf.setTextColor(7, 8, 6) //color del texto


    // pdf.setTableHeaderRow([10000,5522222])
    pdf.fromHTML(id, 80, 75); //todos los datos de mi tabla  Marca HTML

    pdf.setLineWidth(3) //grueso de la linea
    pdf.setDrawColor(251, 152, 4)   // color d ela linea
    pdf.line(80, 750, 525, 750) //  posicion de la linea columna, fila, columna, fila

    pdf.setFontSize(12);

    pdf.text('Emitido por : ' + this.identity.usu_apellido.toUpperCase() + ' ' + this.identity.usu_nombre.toUpperCase(), 80, 775);
    pdf.text('Fecha           : ' + this.today.toLocaleDateString() + "  "+ this.today.getHours()+ ":" + this.today.getMinutes() + ":"+this.today.getSeconds(), 80, 790);
    pdf.save("Detalle-Producto.pdf");
  }


  PDF() {
    var id = document.getElementById("tabMensaje");

    // var imgData='bjdjt-puacn.webp'
    var pdf = new jsPDF({
      orientacion: 'p',
      unit: 'pt',
      format: 'a4',
      posicicion: 3,
      table:50,
      width:50,
    });
    pdf.setFont('times')    //tipo de letra  
    pdf.setFontSize(22);      //tama de letra

    // pdf.addImage(imgData,'JPEG', 15, 40, 18, 16)

    pdf.setTextColor(84, 201, 47)   //color RGB
    pdf.text("Papeleria & Bazar - 'San Francisco' ", 150, 50); //posicion de la letra columna fila

    pdf.setLineWidth(3) //grueso de la linea
    pdf.setDrawColor(251, 152, 4)   // color d ela linea
    pdf.line(80, 67, 525, 67) //  posicion de la linea columna, fila, columna, fila

    // pdf.setTableHeaderRow([1500, 7000]);
    // pdf.table(120, 120, 120, [],120)
    // pdf.table(100, 100,100,[],100)

    pdf.setLineWidth(0.3)
    pdf.setDrawColor(150, 132, 104)
    pdf.setTextColor(7, 8, 6) //color del texto


    // pdf.setTableHeaderRow([10000,5522222])
    pdf.setFontSize(5);
    pdf.fromHTML(id, 80, 75); //todos los datos de mi tabla  Marca HTML

    pdf.setLineWidth(3) //grueso de la linea
    pdf.setDrawColor(251, 152, 4)   // color d ela linea
    pdf.line(80, 750, 525, 750) //  posicion de la linea columna, fila, columna, fila

    pdf.setFontSize(12);

    pdf.text('Emitido por : ' + this.identity.usu_apellido.toUpperCase() + ' ' + this.identity.usu_nombre.toUpperCase(), 80, 775);
    pdf.text('Fecha           : ' + this.today.toLocaleDateString() + "  "+ this.today.getHours()+ ":" + this.today.getMinutes() + ":"+this.today.getSeconds(), 80, 790);
    pdf.save("Listar-Producto.pdf");
  }

  buscarProducto(termino) {
    termino = termino.toLowerCase().replace(/ /g, '');
    this.producto_mostrar = [], [];
    this.countFindeds = 0;
    for (const prod of this.bufferProducto) {
      let nombre = prod.prod_codigo;
      nombre = nombre + prod.prod_descripcion;
      // nombre = nombre + prod.fk_usuario.usu_nombre;
      // nombre = nombre + prod.fk_categoria.cat_descripcion;
      // nombre = nombre + prod.fk_marca.marca_descripcion;
      nombre = nombre.toLowerCase().replace(/ /g, '');;
      if (!prod.prod_codigo) {
        nombre = "0" + nombre;
      }
      if (nombre.indexOf(termino) >= 0) {
        this.countFindeds++;
        this.producto_mostrar.push(prod);
      }
    }
  }
  onPrint() {
    window.print();
  }

  imprimir() {
    window.print();
  }
  ngOnInit() {
    this.getMostrarProducto();
  }


  getMostrarProducto() {
    this._productoServicio.getProducto(this.token).subscribe(
      response => {
        // console.log('la respuesta no llega');
        if (response.producto) {
          // console.log('error no se encuentra  los datos verificar el servidor');
          this.producto_mostrar = response.producto;
          // console.log(this.producto_mostrar);
          this.bufferProducto = response.producto;
          this.reordenarArray('fecha');
          //console.log(response);
        }
        else {
          console.log('no se puede mostrar');
        }
      },
      error => {
        console.log(<any>error);
      }
    )
  }


  regresar() {
    this._router.navigate(['/producto']);
  }

  public asc:boolean;
  cambiarOrden(celda) {
    if (this.asc == true) this.asc = false; else
      if (this.asc == false) this.asc = true;
    this.reordenarArray(celda);
  }

  
  reordenarArray(celda){
    if(this.asc==true){
      if(celda=='descripcion'){
        this.producto_mostrar.sort((menor, mayor) => (menor.prod_descripcion.localeCompare(mayor.prod_descripcion)));
      }else if(celda=='stock'){
        this.producto_mostrar.sort((menor, mayor) => (parseInt(mayor.prod_stock) - parseInt(menor.prod_stock)));
      }else if(celda=='iva'){
        this.producto_mostrar.sort((menor, mayor) => ((mayor.iva) - (menor.iva)));
      }else if(celda=='precio'){
        this.producto_mostrar.sort((menor, mayor) => (parseInt(mayor.prod_precioVenta) - parseInt(menor.prod_precioVenta)));

      }else if(celda=='fecha'){
        this.producto_mostrar.sort((menor,mayor)=>{
          const a = new Date(menor.prod_fecha);
          const b = new Date(mayor.prod_fecha);
          if (a == b) return 0
          if (a > b) return -1
          if (a < b) return 1
        })
        
      }
    } else if(this.asc==false){
      if(celda=='descripcion'){
        this.producto_mostrar.sort((menor, mayor) => (mayor.prod_descripcion.localeCompare(menor.prod_descripcion)));
      }else if(celda=='stock'){
        this.producto_mostrar.sort((menor, mayor) => (parseInt(menor.prod_stock) - parseInt(mayor.prod_stock)));
      }else if(celda=='iva'){
        this.producto_mostrar.sort((menor, mayor) => ((menor.iva) - (mayor.iva)));
      }else if(celda=='precio'){
        this.producto_mostrar.sort((menor, mayor) => (parseInt(menor.prod_precioVenta) - parseInt(mayor.prod_precioVenta)));
      }else if(celda=='fecha'){
        this.producto_mostrar.sort((menor,mayor)=>{
          const a = new Date(menor.prod_fecha);
          const b = new Date(mayor.prod_fecha);
          if (a == b) return 0
          if (a > b) return 1
          if (a < b) return -1
        })
        
      }
    }
    for (let i = 0; i < this.producto_mostrar.length; i++) {
      const element = this.producto_mostrar[i];
      element.index=i+1;
      
    }
  }



}
