import { Component, OnInit } from '@angular/core';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { ConfiguracionService } from 'src/app/services/configuracion.service';
import { Configuracion } from 'src/app/models/configuracion';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Location } from '@angular/common';
import * as jsPDF from 'jspdf';
import { GLOBAL } from 'src/app/services/global';

@Component({
  selector: 'app-configuracion',
  templateUrl: './configuracion.component.html',
  styleUrls: ['./configuracion.component.css'],
  providers :[ConfiguracionService,UsuarioServiceService]
})
export class ConfiguracionComponent implements OnInit {
  public identity;
  public token;
  public url:string;
  public ver:Configuracion;
  constructor(
    private _usuarioservice:UsuarioServiceService,
    private _configuracionservice:ConfiguracionService,
    private _route:ActivatedRoute,
    private _location:Location,
    private _router:Router,
  ) { 
    this.identity=this._usuarioservice.getIdentity();
    this.token=this._usuarioservice.getToken();
    this.url=GLOBAL.url;
    this.initconfigurarion();
    this.mostrar();
    this.getToday();
  }

  detalle() {
    var id = document.getElementById("detalleConfig");
      // var imgData='bjdjt-puacn.webp'
      var pdf = new jsPDF({
        orientacion: '3',
        unit: 'pt',
        format: 'A4',
        posicicion: 3,
      });
      pdf.setFont('times')    //tipo de letra  
      pdf.setFontSize(22);      //tama de letra
  
      // pdf.addImage(imgData,'JPEG', 15, 40, 18, 16)
  
      pdf.setTextColor(84, 201, 47)   //color RGB
      pdf.text("Papeleria & Bazar - 'San Francisco' ", 150, 50); //posicion de la letra columna fila
  
      pdf.setLineWidth(3) //grueso de la linea
      pdf.setDrawColor(251, 152, 4)   // color d ela linea
      pdf.line(80, 67, 525, 67) //  posicion de la linea columna, fila, columna, fila
  
      // pdf.setTableHeaderRow([1500, 7000]);
      // pdf.table(120, 120, 120, [],120)
      // pdf.table(100, 100,100,[],100)
  
      pdf.setLineWidth(0.3)
      pdf.setDrawColor(150, 132, 104)
      pdf.setTextColor(7, 8, 6) //color del texto
  
  
      // pdf.setTableHeaderRow([10000,5522222])
  
  
      pdf.fromHTML(id, 80, 75); //todos los datos de mi tabla  Marca HTML
      
      pdf.setLineWidth(3) //grueso de la linea
      pdf.setDrawColor(251, 152, 4)   // color d ela linea
      pdf.line(80, 750, 525, 750) //  posicion de la linea columna, fila, columna, fila
      
      pdf.setFontSize(12);
      pdf.text('Emitido por : ' + this.identity.usu_apellido + ' ' + this.identity.usu_nombre, 80, 775);
      pdf.text('Fecha           : ' + this.today.toLocaleDateString() + "  "+ this.today.getHours()+ ":" + this.today.getMinutes() + ":"+this.today.getSeconds(), 80, 790);
      pdf.save("Ultima-Configuracion.pdf");
  }

  limpiar() {
    this.initconfigurarion();
   
  }
  regresar(){
    this._router.navigate(['/menu']);
  }
  public cedulaCorrecta: boolean;

  verifyCedula(cedula) {
    this.cedulaCorrecta = false;
    if (cedula.length == 10 || cedula.length == 13) {
      let cedulaCache: any[];
      let sumaPares = 0;
      let sumaImpares = 0;
      let sumaTotal: number;
      let verificador: number;
      let comparador = 10;
      cedulaCache = cedula.split('');
      for (let i = 0; i < 9; i = i + 2) {
        let impar = cedulaCache[i];
        impar = impar * 2;
        if (impar >= 10) {
          impar = impar - 9;
        }
        sumaImpares = sumaImpares + impar;
      }
      for (let i = 1; i < 8; i = i + 2) {
        const par = cedulaCache[i];
        sumaPares = sumaPares + parseInt(par, 0);
      }
      sumaTotal = sumaPares + sumaImpares;
      while (comparador < sumaTotal) {
        comparador = comparador + 10;
      }
      if (comparador - sumaTotal <= 9) {
        verificador = comparador - sumaTotal;
        if (verificador == cedulaCache[9]) {
          this.cedulaCorrecta = true;
        } else {
          this.cedulaCorrecta = false;

        }
      }
    }
  }

  getToday() {
    this.today.getDate();
  }
  ngOnInit() {
    
  }

  initconfigurarion(){
    this.guardar_configuracion=new Configuracion('','','','','','','','','','','','','');
  }

  // limpiar(){
  //   this.initconfigurarion();
  // }
  public guardar_configuracion:Configuracion;
  public alertRegister;
  public today = new Date;

    guardar(){
      // console.log('inicio del metodo');
      this.guardar_configuracion.fk_usuario=this.identity;
      this.today = new Date;
      this.guardar_configuracion.con_fecha = this.today.toString();
      this._configuracionservice.addconfiguracion(this.token, this.guardar_configuracion).subscribe(
        response =>{
          if(response.conf){
              this.guardar_configuracion=response.conf;
              this.alertRegister = 'Se ha registrado Correctamente!..';
              this.mostrar();
                setTimeout(() => {
                this.alertRegister = null;
              }, 3000);
              this.initconfigurarion();
          }
          
        },
        error=> {
          const errorMessage = error as any;
          if (errorMessage != null) {
            const body = JSON.parse(error._body);
            this.alertRegister = body.message;
          }
          this.initconfigurarion();

          // setTimeout(() => {
          //   this.alertRegister = null;
          //   this.limpiar(form);
          // }, 3000);
        }
      )
    }
    public mostrar_conf:Configuracion;
    mostrar(){
      this._configuracionservice.get().subscribe(
        response=>{
          if(response.conf){
              this.mostrar_conf=response.conf;
              // console.log(this.mostrar_conf);
              
          }
        },  
        error=>{
          console.log('Error al Mostrar');
          
        }
      )
    }

    rellenoDatos(config) {
      this.guardar_configuracion = config;
      this.guardar_configuracion._id = null;
      this.guardar_configuracion = JSON.parse(JSON.stringify(config));
    }
   
}
