import { Component, OnInit } from '@angular/core';
import { Proveedor } from 'src/app/models/proveedor';
import { ProveedorService } from 'src/app/services/proveedor.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { GLOBAL } from 'src/app/services/global';
import { ClienteService } from 'src/app/services/cliente.service';
import { Cliente } from 'src/app/models/cliente';
import { Location } from '@angular/common';

@Component({
  selector: 'app-editar-cliente',
  templateUrl: './editar-cliente.component.html',
  styleUrls: ['./editar-cliente.component.css']
})
export class EditarClienteComponent implements OnInit {
  public token;
  public identity;
  public url: string;
  public editar_cliente: Cliente;
  public alertRegister;
  public today = new Date;

  constructor(
    private _clienteServicio: ClienteService,
    private _router: Router,
    private _usuarioServicio: UsuarioServiceService,
    private _route: ActivatedRoute,
    private _location:Location,
  ) {
    this.identity = this._usuarioServicio.getIdentity();
    this.token = this._usuarioServicio.getToken();
    this.url = GLOBAL.url;
    this.getToday();
  }

  ngOnInit() {
    this.mostrarCliente();
  }
  mostrarCliente() {
    this._route.params.forEach((params: Params) => {
      let id = params['id'];
      this._clienteServicio.mostrarUnCliente(id).subscribe(
        response => {
          if (response.cliente) {
            this.editar_cliente = response.cliente;
          }
        },
        error => {
          console.log(<any>error);

        }
      )
    })
  }
  getToday() {
    this.today.getDate();
  }

  actualizar() {
    if (confirm('Desea Guardar')) {
      this.editar_cliente.cli_modificar=this.identity;
      this.today = new Date;

      this.editar_cliente.cli_fechaActualizacion = this.today.toString();
      this._route.params.forEach((params: Params) => {
        let id = params['id'];
        this._clienteServicio.editarCliente(id, this.editar_cliente).subscribe(
          response => {
            if (!response.editar_cliente) {
              // alert('Actualizado Correctamente');
              this.alertRegister = "Actualizado Correctamente"

            }
            else {
              this.alertRegister = "Error al Actualizar";
              // alert('Error Al actualizar');
            }
          },
          error => {
            console.log(<any>error);

          }
        )
        this._router.navigate(['/cliente']);
      })
    }
  }
  regresar() {
    this._location.back();
  }

}
