import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { UsuarioServiceService } from 'src/app/services/usuario-service.service';
import { Usuario } from 'src/app/models/usuario';
import { GLOBAL } from 'src/app/services/global';
import { Location } from '@angular/common';

@Component({
  selector: 'app-editar-usuario',
  templateUrl: './editar-usuario.component.html',
  styleUrls: ['./editar-usuario.component.css'],
  providers: [UsuarioServiceService]
})
export class EditarUsuarioComponent implements OnInit {
  public identity;
  public url: string;
  public token;
  constructor(
    private _usuarioService: UsuarioServiceService,
    private _router: Router,
    private _route: ActivatedRoute,
    private _location: Location,
  ) {
    this.token = this._usuarioService.getToken();
    this.identity = this._usuarioService.getIdentity();
    this.url = GLOBAL.url;
    this.getToday();
  }
  public editar_usu: Usuario;
  mostrarUsuario() {
    this._route.params.forEach((params: Params) => {
      let id = params['id'];
      if (id != '' && id != null) {
        this._usuarioService.mostrarUnUsuario(id).subscribe(
          response => {
            if (response.usuario) {
              this.editar_usu = response.usuario;
              // console.log('metodo buscar un usuario' + this.editar_usu);

            }

          },
          error => {
            console.log(<any>error);
          }
        )
      }
    })
  }
  ngOnInit() {
    this.mostrarUsuario();
  }
  regresar() {
    this._location.back();
  }
  public today = new Date;
  getToday() {
    this.today.getDate();
  }
  public alertRegister;
  actualizar(contrasena) {
    //if (confirm('Desea Guardar')) {
    // this.editarUsuario.prov_modificado=this.identity;
    // this.editarUsuario.prov_fechaActualizacion=this.today.toString();
    this._route.params.forEach((params: Params) => {
      let id = params['id'];
      this._usuarioService.editarUnUsuario(id, this.editar_usu).subscribe(
        response => {
          if (!response.editar_usu) {
            // console.log('exito');
            this.alertRegister = "Actualizado Correctamente"
            console.log('si valio');
            const id = this.editar_usu._id;
            const actualizar = { usu_contrasena: contrasena }
            this._usuarioService.cambiarContrase(id, actualizar).subscribe(
              response => {
                console.log(response);
              }
            )
          }
          else {
            this.alertRegister = "Error al Actualizar";
          }
        },
        error => {
          console.log(<any>error);
        }
      )
      //this._router.navigate(['/proveedor']);
    })
    //}
  }



  cambiarContrase(contrasena) {
    const id = this.editar_usu._id;
    const actualizar = { usu_contrasena: contrasena }
    this._usuarioService.cambiarContrase(id, actualizar).subscribe(
      response => {
        console.log(response);
      }
    )
  }


}
